.login-popup {
    .show-request-username,
    .show-request-reset-password {
        @include font-size(14);
        margin-top: 14px;
        color: $secondary-text-color;

        &:hover,
        &:focus {
            color: inherit;
        }
    }

    .remember {
        position: relative;

        .label {
            @include font-size(14);
            color: $secondary-lighten-text-color;
        }
    }

    .show-registration {
        @include font-size(14);
        color: $secondary-text-color;

        &:hover,
        &:focus {
            color: inherit;
        }
    }

    .content .close {
        @include font-size(20);
        margin: 0 -12px;
    }

}

.login-with-connect {
    h2 {
        border: none;
        margin: 18px 25px 25px;
    }

    .content {
        padding: 24px;

        form {
            margin-top: 0;
        }
    }

    .login-methods {
        display: flex;
        align-items: flex-start;
        margin-bottom: 23px;

        &__item {
            flex: 1;
            padding: 18px 40px;
            border-right: 1px solid #E5E5E5;
            position: relative;

            &:after {
                content: 'or';
                position: absolute;
                top: 50%;
                transform: translate(50%, -50%);
                right: 0;
                background-color: #fff;
                padding: 10px;
            }
            &:last-child {
                border: none;
                &:after {
                    content: none;
                }
            }
        }
    }

    .connect {
        display: flex;
        align-items: center;
        flex-direction: column;

        &-box {
            display: flex;
            align-items: center;
            flex-direction: column;
            font-size: .875rem;
            margin-top: 14px;
            padding: 32px;
            background-color: #F4F4F4;
            color: #4D4D4D;
            width: 100%;

            span a {
                display: inline-block;
                color: #00A2E5;
                text-decoration: underline;
            }
        }

        .site-logo {
            margin-bottom: 14px;
            display: flex;
            align-items: center;
            flex-direction: column;
            font-size: .75rem;

            img {
                margin-top: 10px;
                display: block;
            }
        }

        p {
            font-size: .8125rem;
            text-align: center;
            margin: 32px 0;
            max-width: 400px;

            a {
                color: inherit;
            }
        }

        .button {
            background-color: #00A2E5;
            color: #fff;
            font-size: 1rem;
            text-decoration: none;
            font-weight: 400;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            min-width: 185px;
            height: 40px;
            margin: 8px 15px 0;
            padding: 12px 20px;
            border-radius: 4px;
            transition: background-color .25s;

            img {
                margin-right: 5px;
            }

            &:hover {
                background-color: #007fb5;
            }
        }
    }

    .connect-social-login {
        display: flex;
        align-items: center;
        flex-direction: column;
        margin: 30px 0;

        span {
            margin-bottom: 12px;
        }

        .social-login-options {
            display: flex;
        }

        .social-login__item {
            border: 1px solid #D7D7D7;
            background-color: #fff;
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 0 8px;
            width: 145px;
            height: 40px;
            border-radius: 4px;
            transition: background-color .25s;

            &.rounded {
                border-radius: 50%;
                width: 40px;
                height: 40px;
            }

            &:hover {
                background-color: #eee;
            }
        }
    }

    .submit {
        display: flex;
        justify-content: flex-end;
    }

    input.button {
        display: inline-block;
        width: auto;
        min-width: 136px;
    }

    .signup-section {
        margin: 0 40px;
        padding-top: 24px;
        border-top: 1px solid #E5E5E5;
        text-align: center;
        font-size: .875rem;
    }
}


.login-form {
    .social-icons {
        span {
            @include font-size(24);
        }
    }
}