.ipRangesWidget {
    margin-bottom: 40px;

    .info {
        @include font-size(12);
    }

    [type="submit"] {
        font-size: 0.9em;
        display: inline-block;
        width: auto;
        margin: 10px;
        background: transparent;
        color: inherit;
    }

    input:not(:last-child) {
        margin-left: 0;
    }
}

.ipexamples {
    @include font-size(10);
    padding-top: 2px;

    .example + .example:before {
        display: inline-block;
        content: 'or';
        padding: 0 5px;
    }
}

#institutionMultiIpAdd {
    margin-bottom: 20px;

    label {
        margin: 12px 0;
    }

    @include media('<tablet') {
        padding: 0 1em;
    }

    [type="submit"] {
        margin-top: 0;
        margin-bottom: 0;
        vertical-align: text-top;
    }
}

#institutionMultiIpChange {
    @include media('<tablet') {

        padding: 0 1em;

        thead {
            display: none;
        }

        tr + tr {
            border-top: 1px solid $input-border;
        }

        td:first-of-type {
            padding-left: 0;
        }
        td:nth-of-type(2) {
            padding-top: 1em;
        }
        td:nth-of-type(3) {
            padding-bottom: 1em;
        }
        td:not(:first-of-type) {
            display: block;
            float: left;
            width: 100%;
            padding-right: 0;
        }
    }

    input:not([type="submit"]) {
        margin: 0;
    }

    table {
        margin-bottom: 0.5em;

        td, th {
            padding: 0.5em;
        }

        .error {
            max-width: 150px;
            @include font-size(15);
        }
    }

    label {
        margin: 0;
    }
}

.readOnly {
    .smalltext {
        @include font-size(15);
        margin-bottom: 20px;
        line-height: 1rem;
        color: rgb(99, 102, 106);
        padding: 10px 0;
        font-family: sans-serif;
        font-weight: 400;
    }


    td, th {
        border-bottom: 1px solid $input-border;
        max-width: 100px;
    }
}