// # Defaults
// This is the defaults, feel free to override them as needed in YAMLs
//
// @important! every variable in this file (except constants) - has to have !default flag!
// be careful in this file
// THINK before adding new variable
// Default variables should inherit each other as much as possible -> which should increase branding simplicity
// @see https://github.com/twbs/bootstrap/blob/v4-dev/scss/_variables.scss - for inspiration

// ## global flags/settings
$enable-contrast-function:      true    !default; // enable WCAG checking contrasts
$enable-contrast-function-yiq:  false   !default; // if we need faster check, but not exactly by WCAG, than use get-color-yiq-luminance instead for threshold vs. luminance checking
$enable-enforce-contrasts:      true    !default; // Try to enforce contrast accessibility programmatically
$enable-warnings:               false   !default;
$enable-backward-compatibility: true    !default; // switch major changes under conditions
$enable-text-shadow:            false   !default; // swith usage of text-shadow under conditions (for example :focus on links)

// ## Backward compatibility
// @important! for renamed variables has to be before setting new defaults
// For renaming variables:
// @see imported file: backward-compatibility-variables
@import "backward-compatibility-variables";

// ## accesibility
// WCAG contrast ratio
// @todo: make constants like this? @see: https://www.sitepoint.com/dealing-constants-sass/
// constants:
$wcag__expected-contrast-ratio--level--AA:  4.5;            // level AA - for regular text
$wcag__expected-contrast-ratio--level--AAA: 7;              // level AAA - for regular text
// set level AA:
$wcag__expected-contrast-ratio: $wcag__expected-contrast-ratio--level--AA !default;

// ## colors
// @require color theme functions
@import "../../utility/mixins/scss/colors";

// ## BRANDING!
$primary:           #000        !default;
$secondary:         #a1a1a1     !default;
$success:           #27AE60     !default;
$danger:            #DB3B3B     !default;
$warning:           #F60        !default;
$info:              #4B6A88     !default; // @fixme: deprecate 'note' and use 'info' instead
$dark:              #000        !default;
$light:             #FFF        !default;

// ## theming
$theme-colors: ()               !default;
$theme-colors: map-merge((
    "primary":      $primary,
    "secondary":    $secondary,
    "success":      $success,
    "info":         $info,
    "warning":      $warning,
    "danger":       $danger,
    "light":        $light,
    "dark":         $dark
), $theme-colors);

// Used for decide if dark color should be used - dark above or equal of this threshold of color luminance, light color used below it
$theme-contrasted-threshold:    50                                                  !default;
$theme-text-dark:               theme-color('dark')                                 !default;
$theme-text-light:              theme-color('light')                                !default;
// Set a specific jump point for requesting color jumps
$theme-color-interval:          8%                                                  !default;

// ## animations
$animation-default-duration:    .15s                                                !default;
$animation-border-color:        theme-color('secondary')                            !default;
$animation-pulse-shadow-color:  theme-color('secondary')                            !default;

// ## Borders
$border-width:                  1px                                                 !default;
$border-color:                  theme-color('secondary')                            !default;
$border-radius:                 0                                                   !default;

// ## Focus
$focus-shadow-width:            3px                                                 !default;

// ## Body
$body-bg:                       #fff                                                !default;
$body-color:                    theme-color('dark')                                 !default;

// ## Text
$text-primary-color:            theme-color('primary')                              !default;

// ## Header
$header-bg:                     theme-color('primary')                              !default;
$header-color:                  theme-color('light')                                !default;
$header-border-color:           $header-color                                       !default;
$header-height:                 119px                                               !default;
$header-height-sm:              77px                                                !default;

// ## Footer
$footer-bg:                     #fff                                                !default;
$footer-color:                  theme-color('dark')                                 !default;

// ## Links
// ### default
$link-color:                    theme-color('dark')                                 !default;
$link-hover-color:              pick-hover-color($link-color, 20%)                  !default;
$link-decoration:               underline                                           !default;
$link-hover-decoration:         underline                                           !default;
$link-focus-outline:            1px dotted                                          !default;
$link-disable-color:            pick-hover-color($link-color, 10%)                  !default;
// ### variants
// #### primary
$link-primary-color:            theme-color('primary')                              !default;
$link-primary-hover-color:      pick-hover-color($link-primary-color, 20%)          !default;
$link-primary-decoration:       none                                                !default;
$link-primary-hover-decoration: underline                                           !default;
// #### secondary
$link-secondary-color:          theme-color('secondary')                            !default;
$link-secondary-hover-color:    pick-hover-color($link-secondary-color, 20%)        !default;
$link-secondary-decoration:     none                                                !default;
$link-secondary-hover-decoration: underline                                         !default;
// ### for future testing - uncomment these lines:
//$link-color: #ffc107;

// ## Inputs
$input-bg:                      #fff                                                !default;
$input-border-color:            $border-color                                       !default;
$input-focus-bg:                $input-bg                                           !default;
$input-focus-shadow:            0 0 0 $focus-shadow-width rgba($input-border-color, .4) !default;
$input-placeholder:             theme-color('secondary')                            !default;

// ### Range
$input-range-slider-thumb-color:    theme-color('primary')                          !default;
$input-range-slider-thumb-size:     16px                                            !default;
$input-range-slider-track-height:   4px                                             !default;
$input-range-slider-track-color:    theme-color('light')                            !default;
$input-range-slider-progress-color: theme-color('primary')                          !default;

// ### Checkboxs
$checkbox-radio-border:             theme-color('primary')                          !default;
$checkbox-radio-bg--checked:        theme-color('secondary')                        !default;
$checkbox-radio-border--checked:    $checkbox-radio-bg--checked                     !default;
$checkbox-text-color--checked:      #fff                                            !default;

// ## Buttons
// default
$btn-bg:                        theme-color('secondary')                            !default;
$btn-hover-bg:                  pick-hover-color($btn-bg, $theme-color-interval)    !default; // @fixme legacy - to deprecate
$btn-active-bg:                 pick-hover-color($btn-hover-bg, $theme-color-interval) !default;
$btn-disabled-bg:               $btn-hover-bg                                       !default;
$btn-border:                    $btn-bg                                             !default;
$btn-hover-border:              $btn-hover-bg                                       !default;
$btn-active-border:             $btn-active-bg                                      !default;
$btn-color:                     pick-color($btn-bg)                                 !default;
$btn-hover-color:               pick-color($btn-hover-bg)                           !default;
// legacy
$btn-focus-bg:                  $btn-bg                                             !default;
// appearance
$btn-focus-width:               3px                                                 !default;
$btn-disabled-opacity:          0.4                                                 !default;
$btn-border-radius:             0                                                   !default;
// size
$btn-padding-y:                 11px                                                !default;
$btn-padding-x:                 11px                                                !default;
$btn-font-weight:               bold                                                !default;
$btn-font-family:               $default-font-family                                !default;
$btn-font-size:                 1rem                                                !default; // 16px
// ### outline - "inverse" variant
$btn-inverse-color:             $btn-bg                                             !default;
$btn-inverse-hover-bg:          $btn-bg                                             !default;
$btn-inverse-border:            $btn-inverse-color                                  !default;
// ### for future testing - uncomment these lines:
//$btn-default-bg: #ffc107;
//$btn-border-radius: 3px;
//$btn-font-weight: normal;
//$btn-font-size: 1.25rem;

// ## Tabs
$tab-nav-color:                 theme-color('primary')                              !default;
$tab-nav-bg:                    #fff                                                !default;
$tab-nav-icons-color:           $tab-nav-color                                      !default;
$tab-nav-border-color:          theme-color('secondary')                            !default;
$tab-nav-active-color:          $tab-nav-color                                      !default;
$tab-nav-active-bg:             $tab-nav-bg                                         !default;
$tab-nav-active-icons-color:    $tab-nav-active-color                               !default;
$tab-nav-active-border-color:   $tab-nav-color                                      !default;
$tab-nav-disable-color:         theme-color('secondary')                            !default;
$tab-nav-swip-arrows-color:     #000                                                !default;

// ## Tables
$table-border-color:            theme-color('secondary')                            !default;
$table-header-color:            theme-color('dark')                                 !default;
$table-icon-color:              theme-color('secondary')                            !default;

// ## Media object
$media-gutter-x:                20px                                                !default;

// ## Nav (main nav)
$nav-bg:                        theme-color('primary')                              !default;
$nav-color:                     theme-color('light')                                !default;

// ## Main menu
$menu-dark-bg-color:            theme-color('primary')                              !default;
$menu-text-color:               theme-color('light')                                !default;
$menu-light-bg-color:           #fff                                                !default;

// ## Card
$card-bg:                       #fff                                                !default;
$card-title-color:              theme-color('secondary')                            !default;

// ## Applied facets
$applied-facets-bg:             $body-bg                                            !default;
$applied-facets-color:          theme-color('dark')                                 !default;
$applied-facets-icon-color:     theme-color('dark')                                 !default;
$applied-facets-border-color:   theme-color('dark')                                 !default;
$applied-facets-pinned-color:   theme-color('dark')                                 !default;

// ## Facet
$facets-title-color:            theme-color('secondary')                            !default;
$facet-color:                   theme-color('dark')                                 !default;
$facet-border-color:            theme-color('dark')                                 !default;
$facet-range-date-border-color: theme-color('dark')                                 !default;
$facet-title-color:             theme-color('secondary')                            !default;
$facet-counter-color:           theme-color('dark')                                 !default;
$facet-show-more-color:         theme-color('secondary')                            !default;

// ## Modal
$modal-close-button-color:      theme-color('secondary')                            !default;
$modal-close-button-shadow-color: theme-color('light')                              !default;
$modal-border-color:            theme-color('dark')                                 !default;
$modal-bg:                      #fff                                                !default;
$modal-backdrop-bg:             rgba(189,189,190,.85)                               !default;

// ## Dropzone
$dropzone-border-color:         #ddd                                                !default;
$dropzone-text-color:           #b8b8b8                                             !default;
$dropzone-bg-color:             #fff                                                !default;
$dropzone-bg-hover-color:       #f4f4f4                                             !default;

// ## Dropblock
$dropblock-bg-color:            #fff                                                !default;
$dropblock-text-color:          #000                                                !default;
$dropblock-border-color:        theme-color('dark')                                 !default;
$dropblock-top-border-color:    theme-color('secondary')                            !default;

// ## Mobile Slide
$slide-head-bg:                 theme-color('primary')                              !default;
$slide-head-color:              theme-color('light')                                !default;
$slide-bg:                      #fff                                                !default;
$slide-color:                   theme-color('dark')                                 !default;
$slide-divider:                 theme-color('secondary')                            !default;
$slide-back-arrow-color:        $slide-color                                        !default;
$slide-title-color:             theme-color('secondary')                            !default;

// ## Slide Show
$showcase-slideshow-color:      #fff                                                !default;
$showcase-slideshow-bg-contnet: #000                                                !default;
$showcase-slideshow-current-bg: theme-color('dark')                                 !default;
$showcase-slideshow-current-border: theme-color('dark')                             !default;

// ## LOI
$loi-bg:                        theme-color('light')                                !default;
$loi-border-color:              theme-color('light')                                !default;
$loi-banner-bg:                 #e1e1e1                                             !default;
$loi-dropdown-arrow-color:      #9e9e9e                                             !default;
$loi-content-color:             theme-color('dark')                                 !default;
$loi-content-bg:                $loi-banner-bg                                      !default;
$loi-content-border:            theme-color('dark')                                 !default;
$loi-menu-color:                theme-color('dark')                                 !default;
$loi-menu-divider:              theme-color('dark')                                 !default;

// ## Dashboard Toolbar
$dashboard-toolbar-bg:          $body-bg                                            !default;
$dashboard-toolbar-color:       $body-color                                         !default;
$dashboard-toolbar-shadow:      rgba(0, 38, 58, 0.14)                               !default;
$dashboard-toolbar-submenu-bg:  theme-color('dark')                                 !default;
$dashboard-toolbar-checked:     theme-color('primary')                              !default;


// ## Metric Widget
$metric-primary:                #B2B1F3                                             !default;
$metric-bg:                     #1D1F2D                                             !default;
$metric-bg-light:               #E5E5E5                                             !default;
$metric-download:               #FF9C28                                             !default;
$metric-citation:               #2BF2FF                                             !default;
$metric-abstract:               #ABFF57                                             !default;
$metric-lines:                  #52597A                                             !default;
$metric-empty-text:             theme-color('light')                                !default;
$metric-content-color:          theme-color('light')                                !default;

// ## Atypon Connect
$connect-box-bg:                #F4F4F4                                             !default;
$connect-box-text:              #4D4D4D                                             !default;
$connect-color:                 #00A2E5                                             !default;
$social-login-border:           #D7D7D7                                             !default;


// TO REFACTOR

@if($enable-backward-compatibility) {
    $tab-nav-dropblock-bg:$default-bg-color !default !global;
    $tab-nav-dropblock-link-bg:$secondary-bg-color !default !global;
}

$tab-nav-dropblock-link-bg:                    #fff !default;
$tab-nav-dropblock-bg:          $tab-nav-dropblock-link-bg !default;
$tab-nav-secondary-bg:          theme-color('secondary') !default;
