.facet {

    &__list--applied {

        li {
            color: $applied-facets-color;
            padding: 10px;
            background-color: $applied-facets-bg;
            border: 1px solid $applied-facets-border-color; // So the size matches pinned filter
            @include font-size(12);
            font-weight: 600;
            margin-bottom: 5px;
            margin-right: 5px;

            &.pinned-filter { // LIT-199917 - Temporary styling until UX updates designs
                color:$applied-facets-pinned-color ;
                background-color: lighten($applied-facets-bg, 60%);
                border: 1px solid lighten($applied-facets-border-color, 30%);
            }

            i {
                @include font-size(8);
                margin-left: 12px;
                color: $applied-facets-icon-color;
                cursor: pointer;

            }

            &:last-child {
                margin-right: 0;
            }

        }
    }

    .clear {
        text-align: right;
        display: block;
        @include font-size(10);
        font-weight: normal;
        margin-top: 20px;

        @include media('<tablet') {
            margin-top: 36px;
        }
    }
}
