/// @require math
@import "math";

// Retrieve color Sass maps
@function theme-color($key: "primary") {
    @return map-get($theme-colors, $key);
}

/// get relative luminance of color WCAG accessibility
/// @param {color} $color
/// @return {number} color relative luminance in percent
/// @source Formula: http://www.w3.org/TR/2008/REC-WCAG20-20081211/#relativeluminancedef
/// @source Adapted from: https://gist.github.com/voxpelli/6304812
/// @see https://webaim.org/resources/contrastchecker/ - for testing colors manually
@function get-color-luminance($color) {
    @if($enable-warnings AND type_of($color) != color) {
        @warn "Exception - not a color: #{$color}";
    }
    $rgba: red($color), green($color), blue($color);
    $rgba2: ();
    @for $i from 1 through 3 {
        $rgb: nth($rgba, $i);
        $rgb: $rgb / 255;
        $rgb: if($rgb < .03928, $rgb / 12.92, pow(($rgb + .055) / 1.055, 2.4));
        $rgba2: append($rgba2, $rgb);
    }
    @return (.2126 * nth($rgba2, 1) + .7152 * nth($rgba2, 2) + 0.0722 * nth($rgba2, 3))*100;
}

/// get YIQ color luminance - not by documentation of WCAG, but is sufficient for thresholds, much faster - need less resources to compile
/// @see https://en.wikipedia.org/wiki/YIQ
/// @param {color} $color
/// @return {number} color YIQ luminance in percent
@function get-yiq-color-luminance($color) {
    @if($enable-warnings AND type_of($color) != color) {
        @warn "Exception - not a color: #{$color}";
    }
    @if($enable-contrast-function-yiq == false) {
        @return get-color-luminance($color);
    }
    $r: red($color);
    $g: green($color);
    $b: blue($color);

    $yiq-y: (($r * 299) + ($g * 587) + ($b * 114)) / 1000;

    @return (100/255) * $yiq-y; // percent
}

/// @source Adapted from: https://gist.github.com/voxpelli/6304812
/// @source Formula: http://www.w3.org/TR/2008/REC-WCAG20-20081211/#contrast-ratiodef
@function get-colors-contrast($color1, $color2) {
    $luminance1: get-color-luminance($color1) + 5;
    $luminance2: get-color-luminance($color2) + 5;
    $ratio: $luminance1 / $luminance2;

    @if $luminance2 > $luminance1 {
        $ratio: 1 / $ratio;
    }

    $ratio: round($ratio * 10) / 10;

    @return $ratio;
}

/// Pick contrast color based on threshold and base color
/// @todo NICE TO HAVE feature: create cache for this function if it's too much for processing (doesn't seem necessary, but it will be nice to have)
/// @see https://webaim.org/resources/contrastchecker/ - for testing colors manually
/// @param {color} base color - mostly background color
/// @return {color}
@function pick-color(
    $base,
    $dark: $theme-text-dark,
    $light: $theme-text-light,
    $expected-contrast: $wcag__expected-contrast-ratio
) {
    // save luminance of base color
    $base-color-luminance: get-yiq-color-luminance($base);

    // for transparent BG assume, that we are on light/white background ... we are not able to find what context is button in
    // for outline buttons, that we want to be with light text, we have to set BG to dark bg color
    @if($base == transparent) {
        @if ($body-bg == transparent) {
            $base-color-luminance: 100;
            $base: white;
        } @else {
            $base-color-luminance: get-color-luminance($body-bg);
            $base: $body-bg;
        }

        @if($enable-warnings) {
            @debug "Transparent color was used for base color in pick-color function, please check manualy, we work with it like it is on page BG or as white, if you set body-bg to transparent too - $body-bg is #{$body-bg}";
        }
    }

    // Default use of light color until threshold is hit
    $result: $light;
    // Set dark color dependent on threshold
    @if ($base-color-luminance >= $theme-contrasted-threshold) {
        $result: $dark;
    }

    // Check resulting contrast
    @if($enable-contrast-function) {
        $contrast: get-colors-contrast($base, $result);
        // Warn me if contrast is bad
        @if ($contrast < $expected-contrast AND $enable-warnings AND $enable-enforce-contrasts == false) {
            @warn "Contrast ratio of #{$base} on #{$result} is bad for selected level of accesibility, just #{$contrast} - should be #{$expected-contrast}";
        }

        /// enforce color contrasts if possible AND enabled
        @if($contrast < $expected-contrast AND $enable-enforce-contrasts) {

            $luminance: get-color-luminance($dark);
            $i: 1;
            $interval: ($theme-color-interval * $i);

            @if($enable-warnings) {
                @warn "Contrast ratio of #{$base} on #{$result} was bad for selected level of accesibility, just #{$contrast} - should be #{$expected-contrast} - trying to enforce";
            }
            @while (
                    $contrast < $expected-contrast
                AND $luminance > 0
                AND $luminance < 100
                AND $interval < (100 - $theme-color-interval)
            ) {

                $interval: ($theme-color-interval * $i);

                @if($interval > 100) {
                    $interval: 100%;
                }

                @if ($base-color-luminance >= $theme-contrasted-threshold) {
                    $result: darken($result, $interval);
                } @else {
                    $result: lighten($result, $interval);
                }

                $luminance: get-color-luminance($result);
                $contrast: get-colors-contrast($base, $result);
                $i: $i + 1;
            }

            @if ($contrast < $expected-contrast AND $enable-warnings) {
                @debug "Contrast ratio ENFORCED for #{$result} on #{$base} and is still bad for selected level of accesibility, just #{$contrast} - should be #{$expected-contrast}, please update base color!";
            } @else if ($enable-warnings) {
                @debug "Contrast ratio ENFORCED to new color #{$result} on #{$base} SUCCESSFULLY";
            }
        }
    }

    @return $result;
}

@function pick-hover-color($color, $theme-color-interval: $theme-color-interval) {
    $luminance: get-yiq-color-luminance($color);

    // darken dark colors and lighten light colors
    // AND ensure change will be visible by this condition
    // simple condition is: $luminance >= $theme-contrasted-threshold
    @if (
            ($luminance < $theme-contrasted-threshold AND ($luminance - $theme-color-interval < 0))
        OR ($luminance >= $theme-contrasted-threshold AND ($luminance + $theme-color-interval <= 100))
    ) {
        @return lighten($color, $theme-color-interval);
    } @else {
        @return darken($color, $theme-color-interval);
    }

}

// force inherit color in all states
@mixin inherit-color() {
    &,
    &:hover,
    &:focus,
    &:active {
        color: inherit;
    }
}
