.dashboard-article {
    @include media("<tablet") {
        margin: 0 -15px;
    }
    &__title {
        @include font-size(16);
        line-height: 1.25rem;
        font-weight: 600;
        margin: 0.7rem 0 1.2rem;
    }
    &__label {
        margin: 0.5rem 0;
        font-weight: 600;
        text-transform: capitalize;
        @include font-size(16);
        .submission-meta__item & {
            margin: 0;
        }
    }
    &__value {
    }
    .supplemental-files {

        a {
            text-decoration: underline;
        }
    }
    .funders__table thead th:last-child {
        width: auto;
    }
}

.pbArticleView {
    .table-responsive {
        th,
        td {
            width: auto;
        }
        tr:last-child {
            td,
            th {
                border-bottom: none;
            }
        }
        thead {
            th {
                padding: 5px 10px 5px 0;
                font-weight: 600;
            }
        }
        tbody {
            th,
            td {
                font-weight: normal;
                @include media("<tablet") {
                    border-bottom: none;
                    padding: .5rem .875rem;
                }
            }
        }
        tr {
            @include media("<tablet") {
                padding-top: 0;
                border-bottom: 1px solid $primary-border-color;
                background-color: transparent;
                &:last-child {
                    border-bottom: none;
                }
            }
        }
    }

    .funders, .supplemental-files, .supp-links {
        @include media("<tablet") {
            .card-body {
                padding: .875rem 0 0;
            }
            &__table {
                margin-bottom: 0;
            }
        }
    }
}

.submission-card {
    &__meta {
        .card-body {
            padding-top: 30px;
        }
    }
    table {
        @include media("<tablet") {

            &.separator {
                td {
                    border: none;
                }
            }
            &.expandable .expanded {
                padding: 0;
            }
        }
    }
}

.dashboard-tabs {
    article &.tab {
        background-color: transparent;
        .tab__content {
            background-color: transparent;
            @include media("<tablet") {
                padding: 15px;
            }
        }
    }
    .tab__nav {
        background-color: $tertiary-lighten-bg-color;
        padding: 0 1rem;
        margin-bottom: 0;
        li {
            @include font-size(16);
            margin-bottom: 0;
            a {
                border-bottom: none;
                padding: 12px;
                span {
                    line-height: inherit;
                }
            }
        }
        @include media("<tablet") {
            display: flex;
            li {
                flex: 1;
                text-align: center;
                a {
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
            }
        }
    }
    .tab__pane {
        padding-right: 0;
        padding-left: 0;
        @include media("<tablet") {
            &:focus {
                box-shadow: none;
            }
        }
    }
}

.submission-meta__item {
    margin-top: 1.5rem;
    margin-bottom: 8px;
    &:first-child {
        margin-top: 0;
    }
}

.dashboard-article__meta {
    display: flex;
    @include font-size(16);
    margin-bottom: 0.5rem;
    .meta-item {
        display: flex;
        align-items: center;
        &:not(:first-child):before {
            content: "\25CF";
            margin: 0 .75rem;
            @include font-size(11);
        }
    }
}

.card-header .dashboard-article__meta {
    margin-bottom: 0;
}

.meta__submission-type {
    font-weight: 700;
    @include font-size(16);
    text-transform: capitalize;
}

.meta__submission-date {
    color: #aaa;
}