//@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800');
html{
    font-size: $root-font-size;
}
body {
    background: $default-bg-color;
    color: $primary-text-color;
    font-family: $default-font-family;
    font-size: $default-font-size;
    line-height: 1.4;
    word-wrap: break-word;
    min-height: 100vh;
}

.lock-screen, .noscroll, .modal-open {
    overflow: hidden;

    &.iOS {
        position: fixed;
    }
}

a {
    color: inherit;
    text-decoration: none;
    cursor: pointer;
    &:hover,
    &:focus {
        color: inherit;
        text-decoration: none;
        outline: 0;

    }
    i[class*='icon-'] {
        &:before {
            display: inline-block;
        }
    }

}

img {
    max-width: 100%;
}

.indented {
    & > * {
        text-indent: -15px;
        padding-left: 15px;
        margin-bottom: 15px;
    }
}

.border-bottom {
    padding-bottom: 10px;
    @include border-style(0, 2px, $separators-color);
}

.border-top {
    @include border-style(2px, 0, $primary-lighten-bg-color);
    padding-top: 8px;
}

.inline-icon {
    i {
        padding-right: 5px;
        display: inline-block;
    }
}

.block-icon {
    text-align: center;
    display: block;

    ~ span {
        text-align: center;
        display: block;
    }
}

i[class*='icon-'] {
    transition: transform 0.5s;
    &.js--rotated {
        transform: rotate(180deg);
    }
}

hr {
    &.separator {
        border: 0;
        border-top: 1px solid $secondary-bg-color;
        margin-bottom: 40px;
        margin-top: 40px;
    }
    &.section__separator {
        border: 0;
        border-top: 1px solid $borders;
        margin-top: 15px;
        margin-bottom: 30px;
    }
}

.row {
    &.separator {
        margin-top: 30px;
        margin-left: 0;
        margin-right: 0;
    }
    &[class*='--extra']{
        margin-top: 55px;
        margin-left: 0;
        margin-right: 0;
    }
}

.section__separator {
    @extend .clearfix;

    border-bottom: 1px solid $borders;
    margin-top: 30px;
    margin-bottom: 30px;
}

.reset-padding.reset-padding { //to increase the priority
    padding: 0;
}

.left-side-image {

    img {
        margin-bottom: 0;
        width: 20%;
        float: left;
        padding-right: 20px;
    }
}

span.separator {
    border-left: 1px solid #212121;
    margin-left: 10px;
    padding-left: 10px;
    display: inline-block;
    height: 14px;
}

.fixed{
    position: fixed;
    //background: $white;
}


.fixed-element {
    position: sticky;
    z-index: 5;
    top: $header-height;
    @include media('<desktop') {
        top: $header-height-sm;
    }

    &:before,
    &:after {
        content: '';
        display: table;
    }

    &.header {
        z-index: 10;
    }
}

.success-message {
    @include font-size(12);
    background: $success-message-bg-color;
    color: $success-message-text-color;
    padding: 10px;
    border: 1px solid $success-message-text-color;
}

/*in page builder edit mode*/

#pb-editor {
    .pb-widget-layout {
        padding: 20px 0;

        &:after, &:before {
            content: " ";
            clear: both;
            display: table;
        }
    }

    .header {
        position: static!important;
    }

    .owl-carousel {
        display: block;
    }
}

.pb-ad {
    display: none;
    height: 0;

    @include media('>=desktop') {
        display: table;
        height: auto;
        margin-left: auto;
        margin-right: auto;
    }
}

.note {
    @include font-size(12);
    color: $note-text;
    margin: 0;
}

.error {
    color: red;
    margin: 0;
    @include font-size(12);
}

.success {
    color: green;
    margin: 15px 0;
    @include font-size(12);
}

.warning{
    color: orange;
    margin: 0;
    @include font-size(12);
}

// Allows you to hide text but maintain spacing and borders.
.hidden-text {
    color: transparent;
}

.visibility-hidden{
    visibility: hidden;
    position: absolute;
    left: -999em;
}

/// accessibility classes for screen readers
/// screen reader only
.sr-only {
    @include sr-only();
}
/// screen reader and show on tab focus
.sr-only-focusable {
    @include sr-only();
    @include sr-only-focusable();
    &:focus, &:active {
        /// show on original place - don't mess with anything
        position: absolute !important;
        /// theme - high contrast - we don't need anything but accessibility here
        background: theme-color('light') !important;
        color: theme-color('dark') !important;
        border: 1px solid theme-color('dark') !important;
        padding: 1rem !important;
        box-shadow: 0 0 2px 4px rgba(theme-color('dark'), 0.2);
        border-radius: 3px;
        /// show above all
        z-index: 99999 !important;
        /// reset styling of whatever parent had
        font-weight: bold !important;
        font-size: 1rem !important;
        text-shadow: none !important;
        text-decoration: underline !important;
        text-transform: none !important;
    }
    /// this is probably unnecessary, but let's be nice to testers
    &:hover {
        opacity: .9;
    }
}

/// class for PB editor to show anything what was hidden - for example
/// <h1 class="sr-only pb-editor-show"> Editable in PB, hidden on page </h1>
#pb-editor .pb-editor-show {
    position: static !important;
    width: auto !important;
    height: auto !important;
    padding: unset !important;
    margin: unset !important;
    overflow: visible !important;
    clip: initial !important;
    white-space: initial !important;
    border: initial !important;
    z-index: auto !important;
    color: inherit !important;
    background: inherit !important;
    box-shadow: none !important;
    font-weight: initial !important;
    text-indent: initial !important;
    display: initial !important;
    font-size: initial !important;
}

/// Accessibility
/// Any object with role button, should have pointer cursor
[role="button"] {
    cursor: pointer;
}

.loading {
    background: url("../images/spinner.gif");
    background-size: 140px;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0.5;
}