.submission-list {
    &__list {
        min-height: 627px;
        max-height: 72vh;
        overflow: auto;
    }
    &-item {
        position: relative;
        min-height: 0;
        margin: 0 0 3px;
        padding: 8px 12px;
        background-color: $pd-submission-item;
        @include font-size(14);
        cursor: pointer;
        box-shadow: -2px 2px 2px transparentize($black, 0.88);

        &:hover,
        &.selected {
            background-color: $pd-submission-item-hover;
            box-shadow: -2px 2px 0 0 transparentize($black, 0.88);
            z-index: 2;
            .submission-list-item__label {
                color: $black;
            }
        }

        &__badge {
            display: flex;
            position: absolute;
            right: 8px;
            top: 9px;
            font-weight: 600;
        }
        &__row {
            margin-bottom: 0.875rem;
        }

        &__title {
            @include font-size(16);
            font-weight: 400;
            line-height: 1.25rem;
            margin: 7px 0 0;
        }

        &__label {
            font-weight: 600;
            @include font-size(16);
            text-transform: capitalize;
            color: transparentize($black, 0.46);
        }

        &__value {
            color: #9e9e9e;
        }

        &__meta-group {
            display: flex;
        }

        &__meta {
            font-weight: 600;
            color: transparentize($black, 0.46);
            white-space: nowrap;
            &:not(:first-child):before {
                content: "\25CF";
                margin: 0 0.4rem;
                @include font-size(11);
            }
            &.submission-author, &.meta__meeting {
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        .w-slide__btn {
            position: absolute;
            display: block;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
        }
    }
}

.pd-list-tools {
    margin-bottom: 3px;
    padding: 0.5rem;
    display: flex;
    align-items: center;
    border-radius: 2px;
    &__category {
        margin-right: auto;
        padding-left: 0.2rem;
        > i[class^=icon-] {
            margin-right: 0.75rem;
        }
        .selected-category {
            font-weight: 600;
        }
        .inline-icon {
            padding: 0;
            i {
                @include font-size(8);
                margin-left: 0.6rem;
                margin-top: 0.2rem;
                padding: 0;
                font-weight: 700;
            }
        }
        .selected-category {
            max-width: 162px;
            overflow: hidden;
            text-overflow: ellipsis;
            display: inline-block;
            white-space: nowrap;
        }
        > a {
            border-bottom: 2px solid transparent;
            display: inline-flex;
            align-items: center;
        }

    }
    &__sort, &__filter {
        border-left: 1px solid $separators-color;
        padding: .12rem .5rem;

    }
    &__sort {
        padding-right: 0;
    }

    &__search {
        border: 1px solid transparentize($black, 0.88);
        border-radius: 2px;
        background-color: $white;
        width: 100%;
        form {
            display: flex;
            flex: 1;
            align-items: center;
        }
        i {
            padding: 7px 0.5rem;
            color: $primary-lighten-bg-color;
            font-weight: bold;
        }
        input {
            background-color: transparent;
            border: none;
            margin: 0;
            padding: 7px 8px;
            &:focus {
                outline: none;
            }
        }
        .search-btn {
            font-weight: bold;
            padding: 7px 0.5rem;
            color: transparentize($black, 0.46);
            background-color: transparent;
            border: none;
        }
    }
}

.pd-tools-dropblock {
    position: relative;
    > i[class^=icon-] {
        opacity: 0.54;
        margin-right: 0.75rem;
    }
    &__holder {
        background-color: $white;
        min-width: 200px;
        max-height: 544px;
        overflow-y: auto;
        width: auto;
        z-index: 5;

        @include media("<tablet") {
            max-height: 50vh;
        }

        li {
            border-bottom: 1px solid $separators-color;
            position: relative;
            &.active {
                background-color: $tertiary-bg-color;
                a:before {
                    content: "\e607";
                    font-family: icomoon;
                    position: absolute;
                    left: 0;
                    top: 0;
                    padding: 0.7rem;
                    @include font-size(10);
                    line-height: 1.75;
                }
            }
            a {
                display: block;
                padding: .42rem 1rem .7rem 2.2rem;
                white-space: nowrap;
                &:hover, &:focus {
                    background-color: transparentize($tertiary-bg-color, 0.5);
                }
            }
        }
    }
}

.pd-pagination {
    display: flex;
    margin: 1rem 0;
    align-items: center;
    .pagination__btn {
        margin-left: auto;
        @include font-size(12);
        font-weight: bold;
        height: 100%;
        > span {
            color: $tertiary-text-color;
        }
    }
    .current-page {
        b {
            font-weight: 600;
        }
    }
}

.text-overflow-ellipses {
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
}