.input-group {
    position: relative;
}

.resetPasswordWidget {
    .button {
        max-width: 100px;
    }
    .note {
        color: $primary-link-color;
        @include font-size(16);
        margin-bottom: 20px;

        .bold {
            font-weight: bold;
        }
    }
}

.password-eye-icon {
    position: absolute;
    margin-right: 10px;
    top: 38px;
    right: 0;
    color: #8DA3BC;
    @include font-size(20);
    &:hover {
        cursor: pointer;
    }
}

.delete-account-container {
    display: inline;
}