input.button {
    width: auto;
}

.email {
    .show-change-password {
        @include font-size(12);
        color: $profile-primary-color;

    }
}

#accessTokenForm {
    .submit {
        float: right;
    }
}

.personal-information {
    @extend .clearfix;
}

.managedInstitutions {

    select {
        background-color: transparent;
    }

    option {
        color: $black;
    }



    @include media('<tablet') {
        #pb-page-content .profileMain & {
            position: static !important;
            padding: 15px 0;
        }

    }

}


.header__quick-menu .right-side {
  position: relative;
}
.navigation-login-dropdown-container {
    position: absolute;
    text-align: left;
    box-shadow: 0 3px 7px rgba(0,0,0,.15);
    z-index: 2;
    padding: 10px 20px;
    background: $default-bg-color;
    min-width: 200px;

    ul {
        list-style: none;
        padding: 0;
    }

    li {
        margin-bottom: 8px;
    }

    .right-side & {
        left: auto !important;
        right: 0;
    }
}


.tfa-form-container {
    #hidden-message {
        a {
            font-weight: bold;
        }
    }

    label {
        @include font-size(16);
        color: $profile-primary-color;
        font-weight: normal;
    }

    .OK-btn{
        padding: 12px;
        background: #212121;
        border: none;
        &:disabled {
            opacity: 0.4;
            cursor: not-allowed!important;
        }
    }

    form {
        text-align: left;

        label {
            display: inline-block;
            @include font-size(12);
        }
    }

    .extra-space {
        padding: 20px;
        margin: 20px 0;
        visibility: hidden
    }

    #container-all {
        margin-top: 20px;
    }

}



.access-widget {
    form {
        position: relative;
    }

    .quick-search__button {
        right: 1px;

        @include media('<desktop') {
            bottom: auto;
            top: 1px;

        }
    }

    .account__sort {
        min-width: 245px;
    }
}

#saveSearchDialog a {
    color: $secondary-text-color;
    text-decoration: underline;
}

.billing-labels {
    display: inline-block;
    span {
        display: block;
        margin-bottom: 8px;
    }
}

.billing-data {
    display: inline-block;
    margin-left: 40px;
    label {
        display: block;
        margin-bottom: 10px;
        font-size: .9375rem;
    }
}

.checkout-expand {
    .actions {
        .button {
            @include media('<=tablet') {
                width: 100%;
            }
        }
    }
}

.connect-registration-confirmation {
    width: 400px;
    margin: auto;
    padding: 60px 0;
    display: flex;
    align-items: center;
    flex-direction: column;

    hr {
        width: 100%;
    }

    h3 {
        margin: 0;
        font-size: 1.25rem;
    }

    .connect-registration-confirmation .checkbox-group {
        margin: 30px 20px;

        label {
            display: block;
            font-size: .875rem;
            margin-bottom: 30px;
        }
    }

    .confirmation-footer {
        margin-top: 70px;
        font-size: .75rem;
    }
}








