///
/// Mixins for link appearance
/// @require animate
///
@import "animate";

@mixin make-link(
        $color: $link-color,
        $color-hover: $link-hover-color,
        $decoration: $link-decoration,
        $decoration-hover: $link-hover-decoration,
        $child: false
    ) {
    // smooth
    @include animate($properties: (color, text-decoration, outline, text-shadow));

    // if child, than apply styles only on child, else normal function
    @if($child) {
        & #{$child} {
            // initial state (skipped if color=false)
            @if($color) {
                color: $color;
                text-decoration: $decoration;
            }
        }
        &:hover, &:focus, &:active {
            #{$child} {
                color: $color-hover;
                text-decoration: $decoration-hover;
                i {
                    color: inherit;
                }
            }
        }
    } @else {
        // initial state (skipped if color=false)
        @if($color) {
            color: $color;
            text-decoration: $decoration;
        }

        // hover and focus state
        &:hover, &:focus, &:active {
            color: $color-hover;
            text-decoration: $decoration-hover;
            i {
                color: inherit;
            }
        }

    }

    // focus outline
    &.focus,
    &:focus {
        outline: $link-focus-outline;
        outline-offset: 2px;
        @if($link-focus-outline == 'none' AND $enable-text-shadow AND $color) {
            text-shadow: 0 0 1px rgba($color, .6);
        }
        @else if($link-focus-outline == 'none' AND $enable-text-shadow) {
            text-shadow: 0 0 1px;
        }
        @else if ($color){
            text-decoration-style: double;
            text-decoration-color: rgba($color-hover, .4);
        }
        @else {
            text-decoration-style: double;
        }
    }

}

/// variants

@mixin make-link-primary(
        $color: $link-primary-color,
        $color-hover: $link-primary-hover-color,
        $decoration: $link-primary-decoration,
        $decoration-hover: $link-primary-hover-decoration,
        $child: false
    ) {
    @include make-link(
        $color: $color,
        $color-hover: $color-hover,
        $decoration: $decoration,
        $decoration-hover: $decoration-hover,
        $child: $child
    );
}

@mixin make-link-secondary(
        $color: $link-secondary-color,
        $color-hover: $link-secondary-hover-color,
        $decoration: $link-secondary-decoration,
        $decoration-hover: $link-secondary-hover-decoration,
        $child: false
    ) {
    @include make-link(
        $color: $color,
        $color-hover: $color-hover,
        $decoration: $decoration,
        $decoration-hover: $decoration-hover,
        $child: $child
    );
}
