.accordion--bordered {
  .accordion__content {
    border-left: 2px solid $separators-color;
    padding-left: 0.5em;
  }
}

.accordion {
    &__content {
        width: 100%;
        clear: both;
        display: none;

        &.bordered {
            border-left: 2px solid $separators-color;
            padding-left: 0.5rem;
        }
        &.js--open {
            display: block;
        }
    }

    &__control {
        display: block;
        width: 100%;
        cursor: pointer;
        margin-bottom: 15px;
        @include font-size(18);
        font-weight: bold;

        i[class*=icon-] {
            @include font-size(10);
            margin-top: 8px;
            margin-left: 10px;
        }

        &:hover, &:focus, &:active {
            text-decoration: none;
            color: lighten($primary-text-color, 10%);
        }

        &__text {
            float: left;
        }

        &.js--open {
            // This must be removed, icon name can be changed!
            i.icon-section_arrow_d:before {
                content: "\e60c"
            }
            // use this instead
            i.accordion-expand-icon {
                transform: rotateX(180deg);
            }

            + .accordion__content {
                display: block;
            }
        }

        &.bordered {
            border-bottom: 2px solid $separators-color;
            padding-bottom: 0.5em;
        }

        &.sub {
            color: $tertiary-text-color;
            @include font-size(16);
            font-weight: normal;
        }
    }

    &-tabbed {
        @include font-size(14);

        &__tab {

            &:not(:first-child) {
                border-top: 1px solid;
            }

            &:first-child {
                .accordion-tabbed__tab__control {
                    padding-top: 0;
                }
            }
            &.js--open .accordion-tabbed__content {
                display: block;
            }
        }

        &__control {
            display: block;
            width: 100%;
            font-weight: bold;
            padding: 10px 0;
            cursor: pointer;
            color: $primary-darken-text-color;

            &:hover, &:focus, &:active {
                text-decoration: none;
                color: $primary-text-color;
            }

            .opened & {
                cursor: default;
            }
        }

        &__content {
            border-style: solid;
            border-width: 1px 0 0;
            border-color: $separators-color;
            padding: 10px 0;
            display: none;
        }
    }
    &--centered {
        text-align: center;
        a {
            margin: 0 auto 14px;
            width: auto;
        }
        i {
            float: none;
            display: inline-block;
        }
        .accordion__content {
          padding-bottom: 15px;
        }
    }

    &__control[aria-expanded='true'] + &__content {
        display: block;
    }

    &-with-arrow {
        .accordion-tabbed__control {
            &:after {
                content: "\e60b";
                float: right;
                font-family: icomoon !important;
                @include font-size(8);
                padding-right: 10px;

            }
        }

        .js--open {
            > .accordion-tabbed__control {
                &:after {
                    content: "\e60c";
                }
            }
        }
    }
}

[data-accordion-vport]:not(.accordion-vport--res){
    .accordion-tabbed{
        &__tab{
            border: none;
        }

        &__control{
            cursor: default;
            padding: 15px 0 0;
        }

        &__content{
            border: none;
        }
    }
}