
.tab {
    &__pane {
        @include font-size(14);
    }

    &__content {
        .tab__pane {
            display: none;
            padding-top: 10px;
            &--clear {
                padding-top: 0;
            }

            &.active {
                display: block;
            }
        }
    }

    &__nav {
        @include font-size(11);

        li {
            display: inline-block;
            margin-bottom: 10px;
        }

        i {
            display: block;
            text-align: center;
            color: $tab-nav-icons-color;
            @include font-size(20);
        }

        span {
            line-height: 2;
        }

        a {
            display: block;
            padding: 8px;
            border-bottom: 1px solid $tab-nav-border-color;
        }

        .active {
            a {
                border-bottom-width: 2px;
                border-bottom-color: $tab-nav-active-border-color;
                font-weight: 700;

                i {
                    font-weight: 700;
                    color: $tab-nav-active-icons-color;
                }
            }
        }

        .scroll & {
            padding-right: 20px;
        }

        .disabled {
            color: $link-disable-color;
            cursor: default;
            i {
                color: $link-disable-color;
            }
        }
    }

    &--flex {
        .tab__nav {
            display: flex;

            &:after, &:before {
                display: table;
                clear: both;
                content: " ";
            }

            li {
                flex: 1;
                text-align: center;
            }
        }

    }

    &--aside {
        > .tab {
            &__nav {
                width: 20%;
                float: left;

                li {
                    display: block;
                }

                a {
                    border: none;
                    padding: 10px;
                    border-left: 4px solid transparent;
                }

                .active {
                    a {
                        border-left-color: $tab-nav-active-border-color;
                    }
                }
            }
        }
    }

    &-link:not(.primary-blue-color) {
        color: $link-color;
        font-weight: 600;
    }

    .dropBlock {
        > a {
            background: $tab-nav-dropblock-link-bg;
            font-weight: bold;
            @include font-size(14);
            padding: 10px;
            display: block;
        }

        i {
            @include font-size(10);
            font-weight: bolder;
            color: $tab-nav-icons-color;
            margin-top: 5px;
        }

        ul {
            background: $tab-nav-dropblock-bg;
        }
    }

    &.clean {
        .empty {
            display: none;
        }
    }
    &--aside {
        > .tab {
            &__content {
                width: 80%;
                float: right;
            }
        }
    }

    &_with_bg {
        .tab__nav {
            margin-bottom: 10px;
            box-shadow: inset 0 -1px 0 0 $tab-nav-border-color;
            li {
                margin-bottom: 0;
            }
            a {
                border-bottom: none;
            }
            .active {
                a {
                    border-bottom: 2px solid;
                }
            }
        }
    }
}

.tab--res {
    &.tab--slide {
        .tab__content {
            visibility: hidden;
            opacity: 0;
            //transition: all 250ms;

            .lock-screen & {
                visibility: visible;
                opacity: 1;
            }
        }
    }
    &.tab--aside {
        > .tab {
            &__content {
                width: 100%;
                float: none;
            }
        }
    }

    .dropBlock {
        li {
            display: block;
            margin-bottom: 0px;
            a {
                padding: 8px;
            }
        }
        li.active {
            display: none;
        }

        .tab__nav {
            display: none;

            &.js--open {
                display: block;
                width: 100% !important;
            }

            &:after, &:before {
                display: none;
                clear: both;
                content: " ";
            }

        }

    }
}

.swipe {
    &__wrapper {
        overflow: hidden;
        position: relative;

        .scroll {
            overflow: auto; //LIT-153929
            -ms-overflow-style: none;
            padding: 0 20px;
            overflow: -moz-scrollbars-none;
            &::-webkit-scrollbar {
                display: none;
            }
        }

        &:after, &:before {
            content: " ";
            position: absolute;
            top: 0;
            width: 30px;
            height: calc(100% - 2px);
        }

        &:after {
            right: -10px;
            background: linear-gradient(to right, rgba(255, 255, 255, 0.4) 0%, rgba(255, 255, 255, 1) 100%);
        }

        &:before {
            left: -10px;
            background: linear-gradient(to right, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0.4) 100%);
        }

    }
}

@mixin swip-with-arrow() {
    .tab {
        &__nav {

            li {
                display: none;
                &.active {
                    display: inline-block;
                    white-space: nowrap;
                    text-align: left;
                }
            }
            .arrows-slide {
                display: inline-block;
                text-align: right;
                width: 70px;
                padding: 10px 0;
                border-bottom: 1px solid $tab-nav-border-color;

                &__single {
                    &--disabled {
                        pointer-events: none;
                        opacity: 0.3;
                    }
                }

                .next, .prev {
                    display: inline-block;
                    @include font-size(14);
                    font-weight: 900;
                    color: $tab-nav-swip-arrows-color;
                }
                .next {
                    padding-left: 10px;
                }
                .prev {
                    padding-right: 10px;
                }
            }
            a:after {
                content: "";
                padding: 0;
            }
        }
        &__content {
            padding-top: 10px;
        }
    }
}

.swipe-with-arrows {
    [data-ctrl-res='screen-sm'] {
        @include media('<tablet') {
            @include swip-with-arrow();
        }
    }

    [data-ctrl-res='screen-md'] {
        @include media('<desktop') {
            @include swip-with-arrow();
        }
    }
}

.accordion-tabbed__control {
    @include media('<desktop') {
        i {
            display: none;
        }
    }
}
