.cookiePolicy-popup {
  min-height: 3.25rem;
  height: auto;
  border-bottom: 1px solid #b8b8b8;
  background-color: $dark-grey;
  .container {
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0.375rem 0;
  }
  &__content {
    flex: 1;
    color: #fff;
    h4 {
      margin-bottom: 0.25rem;
    }
    input {
      margin: 0 1rem;
      display: inline-block;
      width: auto;
    }
    a {
      color: #eee;
    }
  }

  &__close {
    @include font-size(24);
    color: #eee;
    opacity: 1;
    &:hover {
      color: #fff;
    }
  }
}
