.purchaseArea {
    background: $white;
    border: 3px solid $primary-commerce-color;
    margin-top: 30px;
    margin-bottom: 30px;
    padding: 10px;
    &--label {
        display: block;
        color: $info-text-color;
        font-size: $h6-font-size;
        padding-bottom: 10px;
    }
    &--list {
        padding: 10px 0;
        a {
            margin: 5px 0;
            //display: inline-block;
            @include media('>=tablet') {
                font-size: $h5-font-size;
            }
            @include media('<tablet') {
                font-size: $h6-font-size;
            }
            &.active {
                font-weight: bold;
            }
        }
        .login-form {
            padding: 0 10px;
        }
    }
    .expand-link {
        display: inline-block;
    }
    .content {
        padding-top: 0;
    }
    .remember {
        position: relative;
    }
    .button {
        width: auto;
        margin-top: 10px;
    }
    .link {
        font-size: $h6-font-size;
    }
    a {
        color: $primary-commerce-color;
    }
}

.responsiveAccessDenialWidget {
    article {
        min-height: auto;
    }
    h1, .parent-item {
        font-weight: 300;
    }
    // LIT-164327
    //.epub-section {
    //    margin-bottom: 15px;
    //}
}

/**************** switches **********************/
.cmn-toggle + label {
    position: relative;
    padding-left: 35px;
    cursor: pointer;
    display: inline-block;
    height: 25px;
    line-height: 25px;
    @include font-size(16);
    -webkit-user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -ms-user-select: none;
    &:before, &:after {
        content: '';
        left: 0;
        position: absolute;
        z-index: 1;
    }
}

.claim-options {
    list-style: none;
    padding: 0;
}

.coolBarDropZone {
    float: right;
    max-width: 50%;
}

.addedMessage {
    font-size: 0px;
    margin: 0px;
    flex-direction: row;
    display: flex;

    &.hidden {
        display: none;
    }

    .article-title {
        padding: 16px;
        background-color: $default-bg-color;
        display: inline-block;
        font-weight: 700;
        text-align: center;
        @include font-size(16);
        width: 100%;
        border: 3px solid $primary-commerce-color;
        margin-bottom: 10px;
        .icon-check {
            @include font-size(25.6);
            vertical-align: middle;
            margin-right: 10px;
            color: $primary-commerce-color;
        }

        @include media("<tablet") {
            width: 100%;
        }
    }

    .show-cart-link {
        display: inline-block;
        color: $default-bg-color;
        padding: 10px;
        margin: 0;
        font-weight: 700;
        @include font-size(16);
        background-color: $primary-commerce-color;
        white-space: nowrap;
        float: right;
        @include media("<tablet") {
            display: block;
            width: 100%;
            margin-top: 15px;
        }
    }

    .article-title-content {
        width: 60%;
        display: inline-flex;
        float: left;
        margin-top: 10px;
        .text {
            float: left;
            margin-top: 0px;
            text-align: left;
            @include media("<tablet") {
                width: 100%;
            }
        }
    }
}

.add-journal-to-cart {
    header {
        line-height: inherit;
        padding: 0;
        border: none;
        position: relative;
        .purchaseMessage {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background: $added-message-color;
            text-align: center;
            color: $white;
            padding: 0;
            opacity: 0.9;
        }
        &:hover{
            position: relative;
            .add-to-cart-msg:not(.added){
                display: block;
                padding: 8px 0;
            }
        }
    }
}

p.itemAddedMsgBox {
    border: none;
    line-height: 60px;
    margin: 0;
    padding: 3px 0 8px 0;
    h3 {
        @include font-size(14);
        font-weight: 600;
        color: $white;
    }
    &:before {
        @include font-size(19);
        vertical-align: middle;
        position: static;
        display: inline;
        margin: 0 10px 0 0;
    }
}

.add-to-cart-msg{
    display: none;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: $added-hover-color;
    text-align: center;
    @include font-size(14);
    font-weight: 600;
    color: $white;
    line-height: 55px;
    margin-left: 0;
    margin-bottom: 0;
    opacity: 0.9;
    [class^="icon-"]{
        @include font-size(19);
        vertical-align: middle;
        margin-right: 10px;
    }
    &.added{
        background: $added-message-color;
    }
}