.owl {
    &-carousel {
        padding: 0 30px;
        .viewport-slider & {
            display: block;
        }

        .pb-dropzone-mode & {
            z-index: auto;
        }

        .owl-nav {
            margin: 0;

            div.owl-nav-active {
                i {
                    color: $link-color;
                    opacity: .5;
                }
            }

            [class*="owl-"] {
                position: absolute;
                top: 50%;
                margin: 0;
                margin-top: -15px;
                font-weight: bold;

                @include media('<phone') {
                    color: $btn-color;
                    background: transparent !important;

                    i {
                        font-weight: bold;
                    }
                    &:hover {
                        color: $btn-hover-color;
                        background: transparent !important;

                    }
                }
            }
        }
    }

    &-prev {
        left: 0;
    }

    &-next {
        right: 0;
    }

    &-controls {
        margin-top: 10px;
        text-align: center;
        -webkit-tap-highlight-color: transparent;

        [class*="owl-"] {
            color: $btn-color;
            @include font-size(14);
            margin: 5px;
            padding: 4px 7px;
            background: $btn-bg;
            display: inline-block;
            cursor: pointer;
            border-radius: 3px;
            font-weight: bold;

            &:hover {
                background: $btn-hover-bg;
            }
        }

    }

    &-dot {
        &:active:focus {
            outline: none;
        }
    }

    &-item {
        transform: translate3d(0, 0, 0);
        -webkit-transform: translate3d(0, 0, 0);
        -moz-transform: translate3d(0, 0, 0);
        -ms-transform: translate3d(0, 0, 0);
    }
}

.slideShow {
    min-height: 215px;

    @include media('<tablet') {
        min-height: 100px;
    }

    &--arrows {
        .owl-next, .owl-prev {
            &[class*=owl-] {
                background-color: transparent;
                color: $btn-inverse-color;
                &:hover, &:focus {
                    background-color: transparent !important;
                    color: $btn-inverse-color;
                }
            }

            i {
                display: inline-block;
                @include font-size(22);
                font-weight: 900;
            }
            span {
                display: none;
            }
        }
    }
}

@include media('<phone') {
    .left-side-image {

        .owl-item img {
            width: 100%;
        }

    }

}

.owl-carousel {
    .owl-dot {
        background: none;
        padding: 0;
        margin: 0;
        &:hover {
            background: none;
        }
    }
}