///
/// Mixins for buttons appearance
/// @require animate
/// @require colors
///

@import "animate";
@import "colors";

/// generic mixin with automatic colors and states
@mixin make-btn(
        $bg: $btn-bg,
        $bg-hover: pick-hover-color($bg),
        $bg-active: pick-hover-color($bg, 10%),
        $border: $bg,
        $border-hover: pick-hover-color($border),
        $border-active: pick-hover-color($border, 10%),
        $color: pick-color($bg), // if set to false, initial state is skipped
        $color-hover: pick-color($bg-hover)
    ) {
    // make buttons smooth on changes
    @include animate($properties: (color, background, border, outline, box-shadow));

    // initial state (skipped if color=false)
    @if($color) {
        color: $color;
        background: $bg;
        border-color: $border;
    }

    // hover state
    &:hover {
        color: $color-hover;
        background: $bg-hover;
        border-color: $border-hover;
    }

    // focus
    &:focus {
        outline: 0;
        box-shadow: 0 0 0 $btn-focus-width rgba($border, .4);
    }

    // disabled state
    &:disabled,
    &.disabled {
        opacity: 0.4;

        // reset hover state on disabled buttons (skipped if color=false)
        &:hover {
            @if($color) {
                color: $color;
                background: $bg;
                border-color: $border;
            }
        }
    }

    // active state
    &:not(:disabled):active,
    &:not(.disabled):active {
        background: $bg-active;
        border-color: $border-active;
    }
}

@mixin make-btn-link(
    $color: $link-color,
    $color-hover: $link-hover-color,
    $decoration: $link-secondary-decoration,
    $decoration-active: $link-secondary-hover-decoration
) {
    font-weight: normal;
    color: $color;
    background-color: transparent;
    border-color: transparent;
    text-decoration: $decoration;

    &:hover {
        color: $color-hover;
        text-decoration: $decoration-active;
        background-color: transparent;
        border-color: transparent;
    }

    &:focus,
    &.focus {
        color: $color-hover;
        text-decoration: $decoration-active;
        border-color: transparent;
        box-shadow: none;
        background: transparent;
        outline: $link-focus-outline;
        outline-offset: 2px;
        @if($link-focus-outline == 'none' AND $enable-text-shadow AND $color) {
            text-shadow: 0 0 1px rgba($color, .6);
        }
        @else if($link-focus-outline == 'none' AND $enable-text-shadow) {
            text-shadow: 0 0 1px;
        }
        @else if ($color){
            text-decoration-style: double;
            text-decoration-color: rgba($color-hover, .4);
        }
        @else {
            text-decoration-style: double;
        }
    }

    &:disabled,
    &.disabled {
        opacity: 0.4;
        color: $color;
        pointer-events: none;
    }

    // active state
    &:not(:disabled):active,
    &:not(.disabled):active {
        background: transparent;
        border-color: transparent;
    }
}

@mixin make-btn-size(
    $padding: $btn-padding-y $btn-padding-x,
    $font-size: $btn-font-size,
    $line-height: $font-size,
    $border-radius: $btn-border-radius
) {
    padding: $padding;
    font-size: $font-size;
    line-height: $font-size;
    border-radius: $border-radius;
}

@mixin make-btn-outline(
    $color: $btn-bg,
    $color-hover: pick-color($color),
    $border: $color,
    $border-hover: $border,
    $border-active: pick-hover-color($border-hover, $theme-color-interval),
    $bg: transparent,
    $bg-hover: $color,
    $bg-active: pick-hover-color($bg-hover, $theme-color-interval)
) {
    @include make-btn(
        $bg: $bg,
        $bg-hover: $bg-hover,
        $bg-active: $bg-active,
        $border: $border,
        $border-hover: $border-hover,
        $border-active: $border-active,
        $color: $color,
        $color-hover: $color-hover
    );
}

/// default variant
@mixin make-btn-default(
    $bg: $btn-bg,
    $bg-hover: $btn-hover-bg,
    $bg-active: $btn-active-bg,
    $border: $btn-border,
    $border-hover: $btn-hover-border,
    $border-active: $btn-active-border,
    $color: $btn-color,
    $color-hover: $btn-hover-color
) {
    @include make-btn(
        $bg: $bg,
        $bg-hover: $bg-hover,
        $bg-active: $bg-active,
        $border: $border,
        $border-hover: $border-hover,
        $border-active: $border-active,
        $color: $color,
        $color-hover: $color-hover
    );
}
