.share {
    &__ctrl {
        @include media('>=tablet') {
            @include font-size(14.08);
            line-height: 0.88rem;
        }
        span {
            margin-left: 10px;
        }
    }
    &__block {
        padding: 15px;
        min-width: 250px;
        background: $white;
        li {
            margin-bottom: 15px;
        }
    }
}

.service-name {
    display: inline-block;

    .hidden-service-name & {
        @extend .visibility-hidden
    }
}

.visible-service-name {
    [class*="addthis_button"] {
        &:after {
            content: attr(title);
            display: inline-block;
        }
    }
}

.addthis.addthis {
    a, [class*="addthis_button"], .at-resp-share-element .at-share-btn {
        display: block;
        width: 100%;
        //color: $primary-text-color;
        margin-bottom: 0;
        line-height: 14px;
    }
    .at-icon-wrapper {
        display: inline-block;
        margin-right: 10px;
        vertical-align: top;
    }
    i {
        @include font-size(20);
        color: inherit;
    }
}
