.supplemental-links {
    &__optional {
        font-weight: 400;
        color: $tertiary-darken-color;
    }
    &__form {
        background: $white;
        padding: 15px 1rem;
        border-radius: 4px;
        box-shadow: 0 4px 4px rgba(0, 0, 0, 0.15), 0 0 8px rgba(0, 0, 0, 0.07);
        display: flex;
        align-items: center;
        margin-bottom: 1rem;
        &.error-input {
            .validation-message {
                display: block;
            }
        }
    }
    &__input-wrapper {
        flex: 1;
        margin: 0 1rem 0;
        position: relative;
    }
    &__input {
        margin: 0;
        padding: 0.2rem 0 0.5rem;
        @include font-size(16);
        border: none;
        border-bottom: 1px solid #dfdfdf;
        transition: border 0.25s;
        &:focus {
            outline: none;
            border-bottom-color: $secondary-text-color;
            box-shadow: none;
        }
    }
    .validation-message {
        display: none;
        position: absolute;
        right: 0;
        top: 0;
        color: #f00;
        @include font-size(16);
        font-weight: 300;
    }
    &__button {
        background-color: transparent;
        color: $primary-text-color;
        display: inline-block;
        border: none;
        text-align: center;
        padding: 7px 12px;
        @include font-size(16);
        font-weight: 600;
        transition: all .25s;
        font-family: Source Sans Pro, sans-serif;
        line-height: 1rem;
        min-width: 104px;
        transform-origin: 50% 0;
        &:hover {
            background-color: transparent;
            color: inherit;
        }
        &:focus {
            background-color: transparent;
            color: $primary-text-color;
            outline: none;
        }
        &:active {
            background-color: rgba($tertiary-bg-color, 0.15);
            transform: scale(0.97);
        }
        &[disabled] {
            pointer-events: none;
            color: $secondary-lighten-text-color;
        }
    }

    &__table {
        th:nth-last-child(2) {
            width: 120px;
        }
        @include media(">=tablet") {
            border-top: 1px solid $separators-color;
            margin-top: .7rem;
            table.separator tbody td {
                padding: 0 8px 0 0;
            }
        }
        td {
            .js-editable {
                padding-left: 0;
            }
            &:first-child {
                .td-text {
                    width: 100%;
                }
            }
        }
    }
    @include media(">=tablet") {
        .jcf-select {
            border: none;
            width: auto;
            min-width: 120px;
            margin: 0;
            padding-left: 0;
            padding-right: 0;
            &:before {
                border: none;
            }
        }
    }

    .js-editable {
        &__validate {
            display: none;
        }
        &.validation-error {
            .js-editable__validate {
                display: block;
                color: $danger;
                @include font-size(16);
                font-weight: 300;
            }
            .js-counter__output {
                display: none;
            }
        }
    }

}