.supplemental-files {

    padding: 1.2rem 1.2rem .85rem;

    .ajax-uploader.multiple {

        .files-list > .files-list__item:first-child {
            margin-top: 0.75rem;
        }

        .files-list__item {
            background-color: #eee;
            padding: 0.75rem;
            align-items: center;
            margin: 0.5rem 0 0;
            border-radius: 4px;
            .remove-file {
                margin-right: 0;
            }
            .icon-warning {
                font-size: 1rem;
                margin-right: 0.5rem;
            }
        }

        [data-state="upload-error"], [data-state="files-error"] {
            background-color: $error-message-bg-color;
            .files-error {
                display: flex;
            }
        }
        .files-error {
            flex: 1;
            justify-content: flex-end;
        }

        .file-name {
            flex: 0 0 40%;
            margin-right: 0;
        }

        .uploader__progress {
            flex: 1;
            margin-top: 0;
            progress {
                flex: 1 1 72%;
            }
            .progress-text {
                flex: 1 1 28%;
            }
        }

        .uploader__message {
            font-size: 1rem;
            justify-content: center;
        }

        .uploader__input-wrapper {
            &.error-input {
                .uploader__input {
                    display: none;
                }
                .uploader__message {
                    display: flex;
                }
            }
        }
    }

    .uploaded-files {
        @include media('>=desktop') {
            td,
            th {
                padding: .75rem;
                &:first-child {
                    width: 40%;
                }

                &:last-child{
                    width: 50px;
                }

                &:nth-child(3) {
                    width: 180px;
                }
            }
        }
    }
    .path-td {
        .td-text {
            @include media("<tablet") {
                width: 100%;
            }
        }
    }
}

#suppfiles-modal {
    .success-icon {
        color: $success-message-text-color;
        margin-left: .5rem;
    }
}