footer {
  position: relative;
  z-index: 12;
    padding-top: 55px;
    color: $footer-color;
    i, a {
        color: $footer-color;
    }
    .border-top {
        border-color: $footer-color;
    }

    a img {
        display: block;
        margin: auto;
    }

    main.with-bg + & {
        background: $tertiary-lighten-bg-color;
    }
}

.footer-top {
    padding: 20px 0;
    background: $footer-bg-color;
    position: relative;
    z-index: 1;

    @include media('<desktop') {
        .separator {
            line-height: 14px;
            margin-top: 20px;

            li {
                padding: 0 15px 0 0;
            }

            li~li {
                padding: 0 15px;

            }
        }

        a {
            color: $footer-color;
            @include font-size(14);
        }
    }
}
.footer-bottom {
    background: $primary-bg-color;
    padding: 17px 0;
    color: $tertiary-text-color;
    @include font-size(12);

    .rlist {
        margin: 0;
    }

    .separator li~li {
        border-left-color: $footer-color;
        margin: 0 8px;
        padding: 0 8px;
    }
}
.sitemap {
    li {
        color: $text-onLight;
        @include font-size(12);
        line-height: 1.8;
    }
}

.copyright {
    float: right;
    color: $footer-color;

    @include media('<desktop') {
        float: none;
        margin-top: 5px;
    }
}

.contact {
    &__address {
        margin-top: 20px;
        color: $footer-color;
        @include font-size(13);
        line-height: 1.5;
    }

    &__nav {
        margin-top: 20px;

        li {
            padding: 0 15px;

            @include media('>=tablet', '<desktop') {
                padding: 0 10px;
            }

            &:first-child {
                padding-left: 0;
            }
        }

        i {
            @include font-size(18);
        }
    }
}

.footer--contact__list {
    margin-bottom: 20px;
    color: $footer-color;
    border-color: rgba($primary-dark-text-color,0.5);
    a {
        font-size: 0.8em;
        opacity: 0.84;
        font-weight: 300;
    }
    li {
        padding: 0 10px;
    }
    &.separator {
        > li ~ li {
            border-color: rgba($primary-dark-text-color,0.5);
        }
    }
}

.pb-normal-view {
    footer {
        position: static;
    }
}