.facetDateChart {
    max-width: 500px;
    margin: 10px 20px 50px;
    position: relative;

    .in-range {
        z-index: 1;
    }
    .out-of-range {
        z-index: 0;
    }
    .tooltip {
        > .tooltiptext {
            visibility: hidden;
            width: 120px;
            background-color: $secondary-bg-color;
            color: $black;
            text-align: center;
            padding: 12px;
            position: absolute;
            z-index: 100;
        }

        &:hover {
            > .tooltiptext {
                visibility: visible;
                margin-top: 50px;
            }
        }
    }

    .bin {
        position: relative;
        margin-right: 1px;

        &-color, &-color-optimal {
            background-color: $tertiary-bg-color;
        }

        &-color-selected, &-color-optimal-selected {
            background-color: $primary-lighten-bg-color;
        }
    }

    &__chart {
        border-bottom: 1px solid $primary-lighten-bg-color;
        padding-bottom: 2px;
        box-sizing: content-box;

    }

    .ui-widget-content {
        background-color: transparent;
    }

    &__slider {

        &.ui-slider {
            &.ui-widget {
                position: absolute;
                top: 0;
                border: none;
                width: 100%;
            }

            .ui-slider {
                &-handle {
                    width: 0;
                    height: 0;
                    background: transparent;
                    border-bottom: none;
                    border-left: 10px solid transparent;
                    border-right: 10px solid transparent;
                    border-top: 15px solid $tertiary-bg-color;
                    border-radius: 10px;

                    &__indicator {
                        width: 1px;
                        display: block;
                        background: $tertiary-bg-color;
                    }
                    &__label {
                        @include font-size(12);
                        display: inline-block;
                        background: $tertiary-bg-color;
                        padding: 6px 12px;
                        color: black;
                        border-radius: 3px;
                        margin-left: -25px;
                        //margin-top: -10px;

                        position: absolute;
                    }
                }
                &-range {
                    background: none;
                }
            }

        }
    }

    &__list {
        margin-top: 70px;

        li {
            padding: 10px 15px;
            margin: 0 -15px;
            border-top: 1px solid $borders;
        }
    }
}


