.profile-menu {
    padding: 30px 0;
    background: $tertiary-lighten-bg-color;
    @include font-size(14);
    font-weight: bold;

    a {
        border: none;
        padding: 10px;
        border-left: 4px solid transparent;
        display: block;
        color: #9d9d9d;

        display: flex;
        justify-content: space-between;
        align-items: center;

        span {
            line-height: 2;
        }
    }

    .active {
        color: #333;
        border-left-color: #333;

    }

    .menu-title:nth-child(3):not(:empty) {
        padding-top: 20px;
        margin-top: 20px;
        border-top: 4px solid $white;
    }

    &--dropBlock {
        margin-left: -15px;
        margin-right: -15px;

        a {
            display: block;
            background: $tertiary-lighten-bg-color;

            @include font-size(12);
            font-weight: bold;
            border-bottom: none;
            padding: 20px;
            border-top: 1px solid #e7e7e7;
            color: #757575;


            &:hover {
                background: #949494;
                color: $default-bg-color;
            }
            span {
                line-height: 1;

            }
        }

        i {
            @include font-size(10);
            font-weight: bolder;
            color: #333;
            margin-top: 5px;
        }
    }

    &--res {
        padding: 0;
        width: 100% !important;

    }



    .item-badge {
        @include font-size(13);
        height: 100%;
        color: white;
        padding: 5px;
        border-radius: 10px;
        &.green{
            background: green;
        }
        &.orange{
            background: orange;
        }
    }

    .menu-title {
        text-transform: uppercase;
        font-size: 1.3rem;
        padding-left: 15px;
        margin-bottom: -1px;

        @include media('<tablet') {
            padding: 25px 0 25px 25px;
        }
    }
}

.connect-profile-menu {
    margin-top: 20px;
}