.recommend-to-librarian {
    .publication-meta {
        @include media("<tablet") {
            margin: 40px 10px;
        }

        @include media("<phone") {
            margin: 15px 10px;
        }

    }


}

.recommendationForm {
    .btn {
        width: auto ;
    }

    label {
        sup {
            font-size: 100%;
            position: static;
        }
    }
}

.specificPublications {

    &-wrapper {
        label {
            color: $black;
            @include font-size(16);
        }

        .dropBlock__holder {
            background: $white;
            width: 100%;
            padding: 10px;
        }

        margin-bottom: 20px;
    }

    &-select {
        max-width: 540px;
        position: relative;

        input {
            background: #fafafa;
            &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
                font-style: italic;
                @include font-size(12);
            }
            &::-moz-placeholder { /* Firefox 19+ */
                font-style: italic;
                @include font-size(12);
            }
            &:-ms-input-placeholder { /* IE 10+ */
                font-style: italic;
                @include font-size(12);
            }
            &:-moz-placeholder { /* Firefox 18- */
                font-style: italic;
                @include font-size(12);
            }
        }

        .icon-Icon_Search {
            position: absolute;
            right: 10px;
            @include font-size(22);
            top: 8px;
        }

        .input-group {
            margin-bottom: 10px;
        }
    }

    &-list {
        max-height: 212px;
        overflow-x: auto;
    }

    &__ctrl {
        border: 1px solid $search-bg-color;
        padding: 10px 8px;
        width: 100%;
        display: block;
        color: $secondary-text-color;
        @include font-size(13);
        font-style: italic;

        i {
            float: right;
            background: #fafafa;
            @include font-size(8);
            font-weight: bold;
            display: block;
            padding: 15px 12px;
            margin: -10px -8px;
            max-width: 38px;
            border-left: 1px solid $search-bg-color;
        }
    }
}