.submission-authors {
    
    table th {
        font-weight: 600;
        padding-bottom: 0;
    }
    
    .author {
        &-surname-td, &-name-td {
            &.to_hide {
                @include media('<tablet') {
                    display: none !important;
                }
            }
        }

        &__row {
            transition: all 0.2s;
            @include media('>=tablet') {
                border-left: 5px solid transparent;
            }
            td {
                vertical-align: bottom;
                padding: 5px 10px 10px 0;
                color: $black;
                border-bottom: none;
                &:first-child {
                    padding-left: 0.5rem;
                    padding-bottom: 5px;
                }
            }
        }

        &__corresponding {
            background-color: $secondary-bg-color;
            border-left-color: $primary-lighten-bg-color;
            .corresponding-notice {
                visibility: visible;
                display: block;
            }
            .corresponding-toggle[type="button"] {
                color: $secondary-text-color;
            }
        }

        &__full-name {
            display: inline-block;
            vertical-align: super;

            > span {
                margin: 0 5px;
                color: $secondary-text-color;
                font-weight: 600;
            }
        }

        &-orcid-td {
            position: relative;
            padding-right: 35px !important;
        }
    }

    .btn--action.hidden {
        display: none;
    }

    .ui-sortable-handle {
        display: inline-block;
        color: $secondary-text-color;
        margin: 0 !important;
        line-height: 1;
    }

    .error {
        @include font-size(14.4);
        margin-top: 0.75rem;
        text-align: right;
        display: none;
    }

    &.not-valid .error {
        display: block;
    }
}

.corresponding-notice {
    visibility: hidden;
    color: $secondary-bg-color;
    display: none;
    @include media(">=tablet") {
        display: inline;
    }
}

.corresponding-toggle[type="button"] {
    padding: 2px 4px;
    background: none;
    color: $secondary-bg-color;
    @include font-size(20.8);
    position: absolute;
    right: 0;
    bottom: 8px;
    margin-right: 10px;
    &:hover, &:active, &:focus {
        color: transparentize($secondary-text-color, 0.5);
        background: none;
    }
}

@include media('>=desktop') {
    .authors__table {
        td,
        th {
            width: 15%;

            &:nth-child(6) {
                width: auto;
            }
        }
    }
}
