.pd-action-bar {
    margin-bottom: 16px;
    border-bottom: 1px solid $primary-border-color;
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;

    > form {
        display: flex;
        flex-wrap: wrap;
    }

    @include media("<tablet") {
        > form {
            width: 100%;
        }
    }

    @include media('>=phone') {
        justify-content: flex-end;
        margin-top: 90px;
    }

    &__info {
        color: rgba(51, 51, 51, .54);
        font-size: $small-font-size;
    }

    .btn {
        min-width: 128px;
        font-weight: 600;
    }

    .pd__actions {
        display: flex;

        &--group {
            display: flex;
            margin-left: .5rem;
            padding-bottom: 1rem;
            flex-wrap: wrap;
        }

        @include media("<tablet") {
            flex-wrap: wrap;
            width: 100%;
            &--group {
                margin: 0 -15px;
                padding: 1rem 15px 1rem;
                border-top: 1px solid $secondary-border-color;
                width: 100%;
                flex: 1 1 100%;

                .btn {
                    flex: 1;

                    + .btn {
                        margin-left: 1rem;

                        &.sm-full-width {
                            margin-left: 0;
                        }
                    }

                    &.sm-full-width + .btn.sm-full-width {
                        margin-top: 1rem;
                    }
                }

                &:first-child {
                    border-top: none;
                    padding-top: 0;
                }
            }
            .sm-full-width {
                flex: 1 1 100%;
            }
        }
    }

    textarea {
        margin-bottom: 0;
    }

    .note {
        width: 100%;
        flex: 0 0 auto;
        @include media("<tablet") {
            width: auto;
            flex: 1;
        }
    }

    .dropzone {
        display: inline-block;
        line-height: normal;
        min-height: 0;
        height: 38px;
        margin-right: 10px;
    }

    &__state {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        font-weight: 600;
        width: 100%;

        @include media("<tablet") {
            justify-content: flex-start;
            padding: 0 1rem;
        }

        .symbol {
            display: inline-flex;
            align-items: center;
            justify-content: center;
            margin-right: 1rem;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            font-size: 1.5rem;
            line-height: 1;
            font-weight: bold;
            color: #fff;
            background-color: $primary-lighten-bg-color;

        }
    }

}

.pdActionBar {
    .toast {
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
    }

    .modal__dialog {
        overflow: visible;
    }
}

.ux-modal-container {
    .pd-action-modal {
        .close {
            display: none;
        }

        @include media("<tablet") {
            .close {
                opacity: 1;
                font-size: 1rem;
                font-weight: bold;
                display: block;
            }

            .modal__footer {
                display: flex;
                justify-content: space-between;

                &::before, &::after {
                    content: none;
                }

                button {
                    margin-left: 0;
                }
            }
        }
    }

    .pd-cancel-modal, .pd-edit-modal {
        .modal__header {
            display: none;
        }

        @include media("<tablet") {
            .modal__dialog {
                padding: 1rem;
            }
            .modal__header {
                display: flex;
                font-weight: 600;
                padding: 0 .25rem 1rem;
                border-bottom: 1px solid $tertiary-bg-color;
            }
            .modal__body, .modal__footer {
                padding: 1rem .25rem;
            }
        }
    }
    .pd-reject-modal {
        .modal__dialog--form {
            @include media(">=tablet") {
                width: 620px;
            }
        }

        textarea {
            margin-bottom: 0;
        }

        .js-counter textarea + .error {
            text-align: right;
        }

        .js-defaultComments .error:not(.hidden) {
            display: inline-block;
            margin-top: 6px;
        }

        .input-group > .error:not(.hidden) {
            display: inline-block;
        }
    }
}


.ui-widget-content.ui-autocomplete {
    z-index: 9999;
}

.quickSearchAutocomplete {
    position: fixed !important; //Overriding inline style
}