.categories-widget {
    .readonly {
        span:not(:first-child):before {
            content: ", ";
        }
    }

}

.jcf-select-submission__select {
    .jcf-optgroup-caption {
        display: none;
    }
}

.tags-input {
    @extend input;
    padding: 1rem;
    min-height: 40px;
    height: auto;
    cursor: pointer;
    position: relative;
    max-height: none;

    @include media(">tablet") {
        padding: .25rem 34px 0 0.25rem;

        &:before, &:after {
            content: "\e60b";
            font-family: icomoon !important;
            @include font-size(7);
            color: transparent;
            display: flex;
            align-items: center;
            position: absolute;
            right: 0;
            top: 50%;
            height: 100%;
            padding: 0 10px;
            transform: translateY(-50%);
        }

        &:after {
            transform: translateY(-50%) rotateZ(0deg);
            transition: all 0.2s;
            color: $separators-color;
        }

        &.js--open:after {
            transform: translateY(-50%) rotateZ(-180deg);
        }
    }
    &__list {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        list-style: none;
        padding: 0;
        margin: 0;
    }

    &__item {
        padding: .5rem 1rem;
        min-height: 1.2rem;
        margin-bottom: 0.25rem;
        background-color: #ddd;
        cursor: text;
        display: flex;
        position: relative;
        flex-direction: column;
        width: 100%;
        align-items: flex-start;

        @include media(">tablet") {
            padding: .5rem .4rem .5rem .6rem;
            margin-right: 0.25rem;
            align-items: center;
            flex-direction: row;
            width: auto;
        }

        > span {
            font-weight: bold;
            @include font-size(14);
            line-height: normal;
            padding: 0.5rem 2.2rem 0.5rem 1rem;
            @include media(">tablet") {
                padding: 0;
                white-space: nowrap;
            }
        }

        & .tags-input__remove {
            margin-left: 0.5rem;
            padding-left: .5rem;
            padding-right: .5rem;
            cursor: pointer;
            position: absolute;
            right: 1.3rem;

            @include media(">tablet") {
                position: static;
                margin-left: .5rem;
            }
            &:before {
                content: "\e622";
                @include font-size(8);
                font-weight: bold;
                font-family: icomoon;
                vertical-align: middle;
            }
        }

        // Child tags
        ul {
            display: block;
            margin: .5rem 0 0;
            padding: 0;
            width: 100%;

            @include media(">tablet") {
                padding: 0 0 0 0.5rem;
                margin: -.2rem 0 -.4rem;
            }

            &:empty {
                display: none;
            }

            .tags-input {
                &__item {
                    margin: 0 0.25rem 0.5rem 0;
                    padding: 0.2rem 0.4rem 0.2rem 0.6rem;
                    background-color: #eee;
                    line-height: 18px;
                    min-height: 0;
                    display: flex;
                    flex-direction: row;

                    @include media(">tablet") {
                        display: inline-flex;
                        margin: 0 0.25rem 0.25rem 0;
                    }

                    &:last-child {
                        margin-right: 0;

                    }

                    span {
                        font-weight: normal;
                        padding: .5rem;
                        @include media(">tablet") {
                            padding: 0;
                        }
                    }

                }
                &__remove {
                    position: static;
                    margin-left: auto;
                    @include media(">tablet") {
                        margin-left: .5rem;
                    }
                }
            }
        }
    }

}

.taxonomies {
    &-input-group {
        position: relative;
    }

    &__add-button {
        margin-top: 0.75rem;
        font-weight: 600;
        display: flex;
        align-items: center;
        i {
            margin-right: 0.5rem;
        }
    }

    &-dropblock {
        @include media(">tablet") {
            width: 100%;
            background-color: $white;
            display: none;
            overflow-y: auto;
            position: absolute;
            z-index: 99;
            box-shadow: 0 0 5px $primary-border-color;
            border: 1px solid $primary-border-color;
            max-height: 35px * 15; // 15 items (item height ~35px)
        }
    }
    &__list {
        & & {
            padding-left: 1.75rem;
            margin: 0 -0.7rem;
            flex: 1 1 100%;
            .taxonomies__item {
                padding: 0.3rem 0.7rem;
                border-bottom: none;
                &:first-child {
                    padding-top: 0.75rem;
                }
            }
        }
        &.collapsed {
            display: none;
        }
    }
    &__item {
        padding: .5rem .7rem;
        border-bottom: 1px solid $separators-color;
        display: flex;
        flex-wrap: wrap;

        &:first-child {
            padding-top: 0.2rem;
        }

        label {
            width: auto !important;
        }
    }
    &__toggle[type="button"] {
        flex: 1;
        padding: 0 .25rem;
        text-align: right;
        background-color: transparent;
        color: $primary-text-color;
        font-size: .5rem;
        &:hover, &:focus {
            box-shadow: none;
            background-color: transparent;
        }
        i {
            display: inline-block;
        }
        &.js--open i {
            transform: rotateZ(-180deg)
        }
    }
}