.list-of-relations {

    p {
        margin: 0;
    }

    &.separator {
        li ~ li {
            margin-top: 25px;
            padding-top: 17px;
        }
    }

    &--date {
        @include font-size(12);
        margin-bottom: 8px;
    }

    &--relationshipType {
        @include font-size(14);
    }
}