/* Animations */
@-webkit-keyframes bg-scrolling-reverse {
    100% {
        background-position: 800% 0;
    }
}

@-moz-keyframes bg-scrolling-reverse {
    100% {
        background-position: 800% 0;
    }
}

@-o-keyframes bg-scrolling-reverse {
    100% {
        background-position: 800% 0;
    }
}

@keyframes bg-scrolling-reverse {
    100% {
        background-position: 800% 0;
    }
}

@-webkit-keyframes bg-scrolling {
    0% {
        background-position: 0 0;
    }
}

@-moz-keyframes bg-scrolling {
    0% {
        background-position: 0 0;
    }
}

@-o-keyframes bg-scrolling {
    0% {
        background-position: 0 0;
    }
}

@keyframes bg-scrolling {
    0% {
        background-position: 0 0;
    }
}

.uploader__dropzone {
    border: 3px solid transparent;

    &.is-dragover {
        border: 3px dashed $primary-bg-color;
        position: relative;

        &:after {
            content: '';
            z-index: 100;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba($white, 0.64);
        }
    }

}

.uploader-basic {
    .uploader {
        margin-bottom: 0;

        &__wrapper {
            padding: 1.2rem 1.2rem 0.85rem;
            margin: 0.25rem;
            border: 3px solid transparent;
        }

        &__input {

            &-wrapper {
                position: relative;
                padding: 16px;
                border: 1px solid rgba(0, 0, 0, 0.12);
                background-color: white;
                border-radius: 4px;
                @include media("<tablet") {
                    padding: 24px;
                }

                &.error-input {
                    border: 1px solid red;
                }

                .error {
                    text-align: center;
                }
            }

            padding: 5px;
        }

        label {
            text-transform: none !important;
        }

        &__dragndrop {
            display: inline;

            [class^="icon-"] {
                @include font-size(24);
                margin-right: 0.5rem;
                vertical-align: sub;
                color: rgba(0, 0, 0, 0.54);
                display: inline-block;
            }

            @include media(">=tablet") {
                .label-mobile {
                    display: none;
                }
            }
            @include media("<tablet") {
                .label-desktop {
                    display: none;
                }
            }
        }

        &__progress {
            height: 1rem;
            margin-top: 0.7rem;
            display: none;
            align-items: center;
            width: auto;

            @include media("<tablet") {
                flex-direction: column;
                align-items: flex-start;
                height: auto;
            }

            progress {
                position: relative;
                -webkit-appearance: none;
                appearance: none;
                flex: 1;
                height: 4px;
                border-radius: $progress-border-radius;
                margin-right: 0.5rem;
                @include media("<tablet") {
                    width: 100%;
                    margin-bottom: 0.5rem;
                }

                &::-webkit-progress-bar {
                    background: $progress-bar-wrapper;
                    border-radius: $progress-border-radius;
                }

                &::-webkit-progress-value {
                    background-color: $progress-bar;
                    border-radius: $progress-border-radius 0 0 $progress-border-radius;
                }

                &[value="100"]::-webkit-progress-value {
                    border-radius: $progress-border-radius
                }

                &:indeterminate:before {
                    content: '';
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    display: block;
                    border-radius: $progress-border-radius;
                    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#b4e391+0,61c419+50,b4e391+100&0+18,1+50,0+83 */
                    background: -moz-linear-gradient(left, transparent 0%, transparent 18%, $progress-bar 50%, transparent 83%, transparent 100%); /* FF3.6-15 */
                    background: -webkit-linear-gradient(left, transparent 0%, transparent 18%, $progress-bar 50%, transparent 83%, transparent 100%); /* Chrome10-25,Safari5.1-6 */
                    background: linear-gradient(to right, transparent 0%, transparent 18%, $progress-bar 50%, transparent 83%, transparent 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */

                    background-size: 90%;
                    -webkit-animation: bg-scrolling-reverse 1.2s infinite; /* Safari 4+ */
                    -moz-animation: bg-scrolling-reverse 1.2s infinite; /* Fx 5+ */
                    -o-animation: bg-scrolling-reverse 1.2s infinite; /* Opera 12+ */
                    animation: bg-scrolling-reverse 1.2s infinite; /* IE 10+ */
                    -webkit-animation-timing-function: linear;
                    -moz-animation-timing-function: linear;
                    -o-animation-timing-function: linear;
                    animation-timing-function: linear;
                }
            }

            .progress-text {
                color: $primary-darken-text-color;
                @include font-size(12);
            }

            &.hide {
                display: none !important;
            }
        }

        &__button {
            white-space: nowrap;
            margin-left: 2ex;
            display: none;
        }

        &__file {
            width: 0.1px;
            height: 0.1px;
            opacity: 0;
            overflow: hidden;
            position: absolute;
            z-index: -1;
            left: -9999px;

            & + label {
                width: 100%;
                margin: 0;
                text-align: center;
                text-overflow: ellipsis;
                font-weight: normal;
                white-space: nowrap;
                cursor: pointer;
                display: inline-block;
                overflow: hidden;
            }
        }
    }

    .error-message {
        display: none;
        align-items: center;
        @include font-size(12);

        @include media("<tablet") {
            margin-top: 0.5rem;
        }

        i {
            @include font-size(36);
            margin-right: 0.75rem;
        }

        &.show {
            display: flex !important;
        }
    }

    .remove-file {
        cursor: pointer;

        i {
            font-weight: 700;
        }
    }

    &.is-dragover {
        .uploader__input-wrapper {
            border-color: $primary-bg-color;
        }
    }

    .success-icon {
        display: none;
        color: $success-message-text-color;
        margin-left: 0.25rem;
        @include font-size(19);
        vertical-align: sub;
        width: 24px;
    }

    .uploader__message {
        display: none;
        align-items: center;
        flex: 1;
        @include media("<tablet") {
            margin-right: 0.5rem;
        }

        i {
            @include font-size(36);
            margin-right: 0.75rem;
        }

        &.show {
            display: flex;
        }

        .remove-error {
            margin-left: auto;
            font-size: 1rem;
            color: $black;
            cursor: pointer;

            i {
                font-size: 1.4rem;
                font-weight: 700;
                margin-right: 0;
            }
        }
    }

    .files-list__item {

        &[data-state="uploading"] {
            .uploader__progress {
                display: flex;
            }

            .remove-file {
                display: block;
            }

            .upload-error, .files-error {
                display: none;
            }
        }

        &[data-state="success"] {
            .success-icon {
                display: inline-block;
            }

            .uploader__progress {
                display: flex;
            }

            .remove-file, .upload-error, .files-error {
                display: none;
            }
        }

        &[data-state="upload-error"] {
            .remove-file {
                display: block;
            }

            .upload-error {
                display: flex;
            }

            .uploader__progress, .files-error {
                display: none;
            }
        }

        &[data-state="files-error"] {
            .remove-file {
                display: block;
            }

            .files-error {
                display: flex;
            }

            .uploader__progress, .upload-error {
                display: none;
            }
        }

    }

    &.single {
        .files-list__item {
            display: none;
            align-items: center;
            padding: 0.5rem 1rem 0.5rem 1.75rem;
            @include media("<tablet") {
                padding: 0;
                align-items: flex-start;
            }

            &.hide {
                display: none;
            }

            .file__inner {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                flex: 1;
                margin-right: -1rem;

                @include media(">=tablet") {
                    align-items: center;
                    flex-direction: row;
                    margin-right: 1.8rem;
                }
            }

            .file-name {
                margin-right: 0.25rem;
            }

            .file-icon {
                @include font-size(40);
                margin-right: 0.7rem;
                @include media("<tablet") {
                    font-size: 1.2rem;
                    margin-bottom: .5rem;
                }
            }

            .file-details {
                flex: 1;
                @include media("<tablet") {
                    width: 100%;
                }
            }

            &[data-state] {
                display: flex;
            }

            &[data-state="dummy"] {
                display: none;
            }

            &[data-state="success"] {
                .remove-file {
                    display: block;
                }

                .uploader__progress {
                    display: none;
                }
            }

            &[data-state="files-error"] {
                .file__inner {
                    display: none;
                }

                .uploader__message, .file-error {
                    display: flex;
                }
            }

            &[data-state="upload-error"] {
                .error-message, .file-error {
                    display: flex;
                    align-items: center;

                    i {
                        @include font-size(19);
                        margin-right: 0.25rem;
                    }
                }
            }
        }

        .uploader__message {
            padding: 1rem;
        }
    }

    &.multiple {

        .files-list > .files-list__item:first-child {
            margin-top: 0.75rem;
        }
        .files-list__item {
            background-color: $grey;
            padding: 0.75rem;
            display: flex;
            align-items: center;
            margin: 0.5rem -.75rem 0;
            border-radius: 4px;

            &[data-state="dummy"] {
                display: none;
            }

            .remove-file {
                margin-right: 10px;
                align-items: center;
                display: flex;
            }

        }

        [data-state="upload-error"], [data-state="files-error"] {
            background-color: $error-message-bg-color;

            .error-message {
                display: flex;
            }
        }

        .error-message {
            flex: 1;
            justify-content: flex-end;
        }

        .file-name {
            flex: 0 0 40%;
            margin-right: 0;

            .file-size {
                display: none;
            }
        }

        .uploader__progress {
            flex: 1;
            margin-top: 0;

            progress {
                flex: 1 1 72%;
            }

            .progress-text {
                flex: 1 1 28%;
            }
        }

        .icon-warning {
            @include font-size(16);
            margin-right: 0.5rem;
        }

        .success-icon {
            margin: 0;
        }

        .uploader__message {
            @include font-size(16);
            justify-content: center;

            i {
                @include font-size(32);
            }

            .remove-error i {
                @include font-size(19);
            }
        }

        .uploader__input-wrapper {
            &.error-input {
                background-color: $error-message-bg-color;

                .uploader__input {
                    display: none;
                }
            }
        }

        @include media("<tablet") {
            .files-list__item {
                margin: .5rem 0;
                flex-direction: column;
                align-items: flex-start;
                position: relative;

                &:first-child {
                    margin-top: 0;
                }
            }
            .file-size {
                display: none;
            }
            .file-name {
                width: 100%;
                margin-bottom: .5rem;

                .file-size {
                    display: inline;
                    margin-left: .25rem;
                }
            }
            .error-message {
                align-items: flex-start;
            }
            .remove-item {
                position: absolute;
                right: 0;
            }
            .uploader__progress {
                width: 100%;
                margin-top: .5rem;
            }

            .uploaded-files {
                margin-bottom: 0;

                table.expandable {
                    margin-bottom: 0;
                }
            }
        }
    }

    .uploaded-files {
        margin-top: 0.7rem;

        .success-icon {
            display: inline-block;
            margin-left: 0.5rem;
        }

        .jcf-select {
            border: none;
            width: auto;
            min-width: 120px;
            margin: 0;
            padding-left: 0;
            padding-right: 0;

            &:before {
                border: none;
            }
        }

        .error-input + .jcf-select {
            padding-left: .25rem;
        }

        .js-editable {
            padding-left: 0;
        }
    }
}

