.submission-info {
    .left-side-content {
        @extend .input-metadata;
        color: $black;
    }
    .contentType-readonly-value {
        text-transform: capitalize;
    }

    .jcf {
        &-compact-multiple {
            max-width: 100%;
        }

        &-select {
            &-text {
                max-width: 90%;
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow: hidden;
            }

            &-opener {
                visibility: hidden;
            }
        }
    }

    .left-side-label {
        &.__top {
            label {
                vertical-align: top;
                margin-top: 8px;
            }
        }

        label ~ textarea, label ~ .jcf-select, label ~ input {
            @extend .input-metadata;
        }

        .radio-group {
            @extend .input-metadata;
            .radio--primary {
                width: 100%;
            }
        }
    }
    .success {
        margin: 0;
    }

    .readonly {
        @include font-size(12);

        &.margin {
            margin-top: 10px;
            display: inline-block;
        }
    }

    .js-counter {
        .warning {
            display: inline-block;
            float: right;
        }
    }

    .input-group {
        textarea {
            display: block;
        }
        .extra-dropzone {
            position: absolute;
            margin: 0 0.5rem;
            left: auto;
            right: 0;
            width: auto;
            height: auto;
            padding: .4rem 0;
            top: auto;
            bottom: 16px;
            @include media('<tablet') {
                bottom: 0;
            }
            .dropzone {
                min-height: 0;
                min-width: 0;
                height: 100%;
                width: 100%;
            }
        }
    }
}

.input-metadata {
    width: 100%;
    display: block;
    margin: 0;
    @include media('>=phone') {
        width: 70%;
    }
}

.left-side-label {
    label.__nomargin {
        margin: 0;
    }
}


