.checkout-expand {
    width: 70%;
}
.credit-card-date-field {
    &:after {
        clear: both;
        display: block;
        content: '';
    }
    select {
        float: right;
        width: 25%;
    }
    label {
        display: inline;
        float: left;
    }
    [name="expYear"] {
        margin-right: 30px;
    }
}
[data-notification="secNumber"] {
    .label {
        display: inline-flex;
        width: 43%;
    }
    input {
        display: inline;
        width: 25%;
    }
}
.checkout-expand {
    .actions {
        .button {
            width: 100%;
        }
    }
}
.login-form {
    .actions {
        .button {
            width: auto;
        }
    }
}
