.custom-date {
    @include font-size(12);
    font-weight: 600;
    color: $facet-color;
    cursor: pointer;

}
.range-slider {

    [class*="index-"]:not(.jcf-range-handle) {
        border: 1px solid $facet-range-date-border-color;
        @include font-size(12);
        padding: 5px 10px;
        border-radius: 2px;
        margin-top: 10px;

    }
}