.list-of-recommended {
    .creative-work__title {
        font-weight: normal;
        margin-bottom: 0;
    }
    li .creative-work {
        border-bottom: none;
        padding-bottom: 0;
    }
    .loa {
        margin-bottom: 5px ;
    }


}

.lot {
    .item__body {
        .filled--blog, .blog__primary-topic, .item__title {
            display: block;
            font-weight: 400;
            color: $creative-title-color;
            @include font-size(14);
            margin: 10px 0;
        }

        .item {
            &__title {
                @include font-size(16);
            }

            &__detail {
                overflow-y: hidden;
                position: relative;
                color: $creative-color;
                font-weight: 700;
                @include font-size(13);
                text-transform: uppercase;
            }
        }
        .loa {
            clear: both;

        }

        .accordion {
            margin-top: 10px;

            &__control {
                @include font-size(14);
                color: $creative-title-color;

                i[class*=icon-] {
                    margin-left: 0;
                    margin-right: 10px;
                    @include font-size(8);
                    font-weight: bold;
                }
            }

            &__content {
                background: $tertiary-lighten-bg-color;
                padding: 5px 20px;
            }
        }
    }
}