.co-branding {
    section {
        margin-top: 60px;
    }

    .border-bottom {
        margin-bottom: 30px;
    }

    label {
        @include font-size(14);
    }

    .btn {
        background: transparent;
        color: $profile-primary-color;
        @include font-size(12);

        &:hover {
            text-decoration: underline;
        }
    }
    input[type=file]::-webkit-file-upload-button { /* chromes and blink button */
        cursor: pointer;
    }
}

