.auto-hide {
  &-bar {
    top: 0;
  }

  &-primary-bar, &-secondary-bar {
    background: white;
  }

  &-wrapper {
    position: sticky;
    top: 0;
    z-index: 10;
    @include media('>=tablet') {
      transition: all .5s;
    }
  }
}

.secondary-bar--sticky {
  -webkit-backface-visibility: hidden;

  > div {
    -webkit-transform: translate3d(0, 0, 0);
  }
}