// Margin and Padding
$spacer: 1rem !default;
$spacers: (
        0: 0,
        1: ($spacer * .25),
        2: ($spacer * .5),
        3: $spacer,
        4: ($spacer * 1.5),
        5: ($spacer * 3),
        6 : ($spacer * 2),
        auto: auto
) !default;

@each $prop, $abbrev in (margin: m, padding: p) {
    @each $size, $length in $spacers {
        /*
        This part is used for all viewports.

        "$abbrev" stands for the class's first litter which indicates
        if it's an M => Used for Margins or P => Used for padding.

        "$size" stands for the amount of margin or padding that will
        be applied, the $size is represented in number from 0-6, you can
        add new entries if you needed in the @spacer and use it in your
        own class.

        "$prop" is the same as the "$abbrev" but for the attribute name.

        "$length" is the same as "$size" but for the attribute value.

        "x" and "y" refer to X and Y axis, so py-6 => padding top and bottom 6

        "t/b/r/l" are Top/Bottom/Right/Left
        */
        .#{$abbrev}-#{$size} {
            #{$prop}: $length !important;
        }
        .#{$abbrev}t-#{$size},
        .#{$abbrev}y-#{$size} {
            #{$prop}-top: $length !important;
        }
        .#{$abbrev}r-#{$size},
        .#{$abbrev}x-#{$size} {
            #{$prop}-right: $length !important;
        }
        .#{$abbrev}b-#{$size},
        .#{$abbrev}y-#{$size} {
            #{$prop}-bottom: $length !important;
        }
        .#{$abbrev}l-#{$size},
        .#{$abbrev}x-#{$size} {
            #{$prop}-left: $length !important;
        }
        /*
          This part is used for the rest of the viewports.

          As always:
          "xs" refers to x-small.
          "sm" refers to small.
          "md" refers to medium.
          "lg" refers to large.
        */
        @include media('<=phone') {
            .#{$abbrev}-xs-#{$size} {
                #{$prop}: $length !important;
            }
            .#{$abbrev}t-xs-#{$size},
            .#{$abbrev}y-xs-#{$size} {
                #{$prop}-top: $length !important;
            }
            .#{$abbrev}r-xs-#{$size},
            .#{$abbrev}x-xs-#{$size} {
                #{$prop}-right: $length !important;
            }
            .#{$abbrev}b-xs-#{$size},
            .#{$abbrev}y-xs-#{$size} {
                #{$prop}-bottom: $length !important;
            }
            .#{$abbrev}l-xs-#{$size},
            .#{$abbrev}x-xs-#{$size} {
                #{$prop}-left: $length !important;
            }
        }
        @include media('>phone' , '<=tablet') {
            .#{$abbrev}-sm-#{$size} {
                #{$prop}: $length !important;
            }
            .#{$abbrev}t-sm-#{$size},
            .#{$abbrev}y-sm-#{$size} {
                #{$prop}-top: $length !important;
            }
            .#{$abbrev}r-sm-#{$size},
            .#{$abbrev}x-sm-#{$size} {
                #{$prop}-right: $length !important;
            }
            .#{$abbrev}b-sm-#{$size},
            .#{$abbrev}y-sm-#{$size} {
                #{$prop}-bottom: $length !important;
            }
            .#{$abbrev}l-sm-#{$size},
            .#{$abbrev}x-sm-#{$size} {
                #{$prop}-left: $length !important;
            }
        }
        @include media('>tablet' , '<=desktop') {
            .#{$abbrev}-md-#{$size} {
                #{$prop}: $length !important;
            }
            .#{$abbrev}t-md-#{$size},
            .#{$abbrev}y-md-#{$size} {
                #{$prop}-top: $length !important;
            }
            .#{$abbrev}r-md-#{$size},
            .#{$abbrev}x-md-#{$size} {
                #{$prop}-right: $length !important;
            }
            .#{$abbrev}b-md-#{$size},
            .#{$abbrev}y-md-#{$size} {
                #{$prop}-bottom: $length !important;
            }
            .#{$abbrev}l-md-#{$size},
            .#{$abbrev}x-md-#{$size} {
                #{$prop}-left: $length !important;
            }
        }
        @include media('>desktop') {
            .#{$abbrev}-lg-#{$size} {
                #{$prop}: $length !important;
            }
            .#{$abbrev}t-lg-#{$size},
            .#{$abbrev}y-lg-#{$size} {
                #{$prop}-top: $length !important;
            }
            .#{$abbrev}r-lg-#{$size},
            .#{$abbrev}x-lg-#{$size} {
                #{$prop}-right: $length !important;
            }
            .#{$abbrev}b-lg-#{$size},
            .#{$abbrev}y-lg-#{$size} {
                #{$prop}-bottom: $length !important;
            }
            .#{$abbrev}l-lg-#{$size},
            .#{$abbrev}x-lg-#{$size} {
                #{$prop}-left: $length !important;
            }
        }
    }
}